import React from "react";
import Setting from "./setting.js";
import Link from "valuelink";
import AdminService from "../../services/admin.service.js";

let links = {};
export default class AdminSetting extends React.Component {
  constructor() {
    super();
    this.state = {
      Password: "",
      NewPassword: "",
      ConfirmPassword: "",
      ShowAlertModal: false,
      Message: ""
    };
    this.submit = this.submit.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.Reset = this.Reset.bind(this);
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  submit() {
    if (this.valid()) {
      AdminService.changePassword(this.state).then(response => {
        this.setState({
          ShowAlertModal: true,
          Message: response.data.Message
        });
      });
    }
  }
  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }

  Reset() {
    this.setState({ Password: "", NewPassword: "", ConfirmPassword: "" });
  }
  render() {
    links = {
      passwordLink: Link.state(this, "Password")
        .check(x => x, "Password Required")
        .check(
          x => x.length >= 6,
          "Password must becontain at least six characters"
        ),
      newpasswordLink: Link.state(this, "NewPassword")
        .check(x => x, "New Password Required")
        .check(
          x => x.length >= 6,
          "Password must becontain at least six characters"
        ),
      confirmPasswordLink: Link.state(this, "ConfirmPassword").check(
        x => x == this.state.NewPassword,
        "new Password and Confirm Password should be same."
      )
    };
    const obj = {
      links,
      save: this.submit,
      Reset: this.Reset,
      CloseModal: this.CloseModal,
      model: this.state
    };
    return <Setting {...obj} />;
  }
}



