import React from "react";
import Pagination from "react-js-pagination";
import Helper from "../../helpers/utility.helper.js";
import { Link } from "react-router-dom";
import { TextArea } from "valuelink/tags";
import VInput from "Components/common/VInput";
const Detail = props => {
    return (
        <div class="container-fluid">
            <div class="row margin-top-8x margin-bottom-8x align-center warehouse-dtl">
                <div class="col-lg-12">
                    <h3>
                        Request Detail
                    </h3>
                    <div class="form-wrap margin-top-6x">
                        <div class="form-group align-left">
                            <label>Name</label>
                            <p>{props.State.Name}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Email</label>
                            <p>{props.State.Email}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Phone</label>
                            <p>{props.State.Phone}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Warehouse</label>
                            <p>{props.State.Warehouse.Name}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Address</label>
                            <p>{props.State.Warehouse.Address}</p>
                        </div>

                        {props.State.Type == "Enquire" ? (<div class="form-group align-left">
                            <label>Space Required</label>
                            <p>{props.State.SpaceRequired}</p>
                        </div>) : ""}

                        {props.State.Type == "Enquire" ? (<div class="form-group align-left">
                            <label>From Date</label>
                            <p>{Helper.ConvertDateToString(props.State.FromDate)}</p>
                        </div>) : ""}

                        {props.State.Type == "Enquire" ? (<div class="form-group align-left">
                            <label>To Date</label>
                            <p>{Helper.ConvertDateToString(props.State.ToDate)}</p>
                        </div>) : ""}
                        <div class="form-group align-left">
                            <label>Subject</label>
                            <p>{props.State.Subject}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Created Date</label>
                            <p>{Helper.ConvertDateToString(props.State.CreateDate)}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Message</label>
                            <p>{props.State.Message}</p>
                        </div>
                        <div class="form-group align-left">
                            <button class="btn btn-success" onClick={props.goBack}>
                                Back
                           </button>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    );
};
module.exports = Detail;



