import React from "react";
import VInput from "Components/common/VInput";
import RichTextEditor from "AdminComponents/common/Editor.js";
const blogObj = props => {
  const errorModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const SuccessModal = {
    ModalCss: props.SuccessModal ? "in" : "",
    ModalStyle: { display: props.SuccessModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <h3 class="margin-bottom-6x">Add New Blog</h3>
          <div class="margin-bottom-4x align-center">
            <div class="imageupload panel panel-default">
              <div class="panel-heading clearfix">
                <h3 class="panel-title pull-left">Upload Image</h3>
              </div>
              <div class="file-tab panel-body">
                <label class="btn btn-success btn-file">
                  <span>Browse</span>
                  <input
                    id="blogImage"
                    type="file"
                    onChange={props.UploadImage}
                    name="image-file"
                  />
                </label>
                <button
                  type="button"
                  onClick={props.RemoveImage}
                  class="btn btn-danger"
                >
                  Remove
                </button>
              </div>
            </div>
            <div class="full-width">
              <div class="form-wrap margin-top-6x">
                <h3 />
                <div class="form-group align-left margin-top-6x">
                  <label>Blog Name / Title</label>
                  <VInput
                    type="text"
                    className="form-control"
                    name="Name"
                    placeholder="Blog Name*"
                    maxLength="250"
                    valueLink={props.links.blogNameLink}
                  />
                </div>
                <div class="form-group align-left">
                  <label>By</label>
                  <VInput
                    type="text"
                    className="form-control"
                    name="By"
                    placeholder="by*"
                    maxLength="50"
                    valueLink={props.links.bylink}
                  />
                </div>
                <div class="form-group align-left margin-top-6x">
                  <label>Description</label>
                  {/* <VInput
                  type="text"
                  className="form-control"
                  name="Description"
                  placeholder="Description*"
                  maxLength="50"
                  valueLink={props.links.descriptionlink}
                /> */}
                  <RichTextEditor {...props} />
                </div>
                <div class="form-group align-center margin-top-6x setting-page">
                  <button class="btn btn-success" onClick={props.saveBlog}>
                    Add Blog
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + errorModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={errorModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={errorModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{errorModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="popup-btn"
                    value="Ok"
                    onClick={errorModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>{SuccessModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <a href="/admin/blogs" class="popup-btn">
                    Ok
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = blogObj;



