import React from "react";
import Pagination from "react-js-pagination";

const userwarelist = props => {
  let listObj = "";
  let name = "";
  let email = "";
  let roleList = "";
  if (Object.keys(props.model.userInfo).length) {
    name =
      props.model.userInfo[0].FirstName +
      " " +
      props.model.userInfo[0].LastName;
    email = props.model.userInfo[0].Email;
  }

  if (Object.keys(props.model.userRoleList).length) {
    roleList = props.model.userRoleList.map(item => {
      return (
        <tr>
          <td>
            {item.Role}
          </td>
          <td>
            {item.FirstName}
          </td>
          <td>
            {item.Email}
          </td>
        </tr>
      );
    });
  }

  if (Object.keys(props.model.userWareList).length) {
    listObj = props.model.userWareList.map(item => {
      let fromdate = "";
      let todate = "";
      let finalCost = "";
      let wareStatus = "";
      let wareName = "";
      let wareId = "";

      if (item.data) {
        wareName = item.data.Name;
        wareId = item.data._id;
      }
      if (item.Pallets.length) {
        fromdate = item.Pallets[0].FromDate;
        todate = item.Pallets[0].ToDate;
      }

      if (item.TotalCostBreakDown)
        finalCost = item.TotalCostBreakDown.FinalCost;

      switch (item.Status) {
        case 0:
          wareStatus = "Category Added";
          break;
        case 1:
          wareStatus = "Quote Pending";
          break;
        case 2:
          wareStatus = "Booking Pending";
          break;
        case 3:
          wareStatus = "Booked";
          break;
      }
      return (
        <tr>
          <td>
            <a href={"/admin/waredetail/" + wareId}>
              {wareName}
            </a>
          </td>
          <td>
            {fromdate} - {todate}
          </td>
          <td>
            {wareStatus}
          </td>
          <td class="green">
            &pound;{finalCost}
          </td>
        </tr>
      );
    });
  } else {
    listObj = (
      <div class="no-result-found" style={{ marginLeft: "50%" }}>
        <p>No Result Found</p>
      </div>
    );
  }
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <div class="table-wrap">
            <div class="">
              <div class="-">
                <table class="table table-bordered">
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Email Address</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        {name}
                      </td>
                      <td>
                        {email}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>

          <div class="table-div">
            <div class="">
              <table class="table table-bordered">
                <thead>
                  <tr>
                    <th>Role</th>
                    <th>Name</th>
                    <th>Email</th>
                  </tr>
                </thead>
                <tbody>
                  {roleList}
                </tbody>
              </table>
            </div>
          </div>

          <div class="table-div">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Warehouse </th>
                    <th>From and to</th>
                    <th>Status</th>
                    <th>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {listObj}
                </tbody>
              </table>
            </div>
            <div>
              <Pagination
                hideDisabled
                activePage={props.model.activePage}
                itemsCountPerPage={10}
                totalItemsCount={props.model.totalCount}
                pageRangeDisplayed={5}
                onChange={props.handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = userwarelist;



