import React from "react";
import List from "./list.js";
import BlogService from "AdminServices/blog.service.js";
let skip = 1;
export default class BlogList extends React.Component {
  constructor() {
    super();
    this.state = {
      activePage: 1,
      totalCount: 0,
      adminBlogObj: {},
      ShowAlertModal: false,
      ModalBody: "",
      SuccessModal: false,
      errorModal: false,
      ShowMessageModal: false,
      BlogDetail: {}
    };
  }
  getAllBlogs(skipCount) {
    BlogService.getSkipBlockList(skipCount).then(response => {
      this.setState({
        adminBlogObj: response.data,
        BlogDetail: {},
        ShowAlertModal: false,
        SuccessModal: false,
        errorModal: false,
        ShowMessageModal: false,
        ModalBody: ""
      });
    });
  }
  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      SuccessModal: false,
      errorModal: false,
      ShowMessageModal: false,
      ModalBody: ""
    });
  }
  DeleteSelectedBlog(blog) {
    this.setState({ BlogDetail: blog, ShowAlertModal: true });
  }
  deleteBlog() {
    this.setState({ ShowAlertModal: false });
    BlogService.deleteBlog(this.state.BlogDetail._id).then(response => {
      if (response.data.Status) {
        this.getAllBlogs(0);
      }
    });
  }
  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber });
    let skipCount = (pageNumber - skip) * 10;
    this.getAllBlogs(skipCount);
  }
  viewBlog(Id) {}
  componentWillMount() {
    this.getAllBlogs(0);
    BlogService.all().then(response => {
      this.setState({ totalCount: response.data.length });
    });
  }
  render() {
    const listObj = {
      model: this.state,
      deleteBlog: this.deleteBlog.bind(this),
      handlePageChange: this.handlePageChange.bind(this),
      viewBlog: this.viewBlog.bind(this),
      SuccessModal: this.state.SuccessModal,
      ShowAlertModal: this.state.ShowAlertModal,
      ShowMessageModal: this.state.ShowMessageModal,
      ModalBody: this.state.ModalBody,
      showDeleteAlert: this.DeleteSelectedBlog.bind(this),
      onClose: this.CloseModal.bind(this)
    };
    return <List {...listObj} />;
  }
}



