import React, { Component } from "react";
import NewsLetter from "./newsLetter.js";
import Service from "AdminServices/newsLetter.service.js";
export default class newsLetterContainer extends Component {
  constructor() {
    super();
    this.state = {
      result: [],
      pageNo: 1,
      total: 0
    };
    this.getMembers = this.getMembers.bind(this);
  }
  componentDidMount() {
    this.getMembers(1);
  }
  getMembers(pageNo) {
    Service.All(pageNo)
      .then(res => {
        const data = JSON.parse(res.data.body);
        console.log(data.total_items);
        console.log(data.members);
        this.setState({
          result: data.members,
          total: data.total_items,
          pageNo
        });
      })
      .catch(err => {
        console.log(err);
      });
  }
  render() {
    const model = {
      model: this.state,
      getMembers: this.getMembers
    };

    return <NewsLetter {...model} />;
  }
}



