import React from "react";
import Add from "./add.js";
import UserService from "AdminServices/admin.service.js";
export default class ManageVideos extends React.Component {
  constructor() {
    super();
    this.state = {
      model: {
        Url: "",
        Description: ""
      },
      videoList: {},
      ShowAlertModal: false,
      Message: "",
      buttontext: "Save",
      totalCount: 0
    };
    this.handleChange = this.handleChange.bind(this);
    this.save = this.save.bind(this);
    this.reset = this.reset.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.edit = this.edit.bind(this);
  }
  allVideos() {
    UserService.all().then(response => {
      this.setState({
        videoList: response.data,
        totalCount: response.data.length
      });
    });
  }
  componentWillMount() {
    this.allVideos();
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ model: { ...this.state.model, [name]: value } });
  }
  save() {
    if (this.state.totalCount == 2 && !this.state.model._id) {
      this.setState({
        ShowAlertModal: true,
        Message: "Only two videos allowed"
      });
      return false;
    }
    let require = true;
    if (this.state.model.Description == "") {
      this.setState({
        ShowAlertModal: true,
        Message: "please add a description"
      });
      require = false;
    }
    if (this.state.model.Url == "") {
      this.setState({
        ShowAlertModal: true,
        Message: "please add an url"
      });
      require = false;
    }
    if (require) {
      UserService.manageVideo(this.state.model).then(response => {
        this.setState({
          ShowAlertModal: true,
          Message: response.data.Message
        });
        this.allVideos();
        this.reset();
      });
    }
  }
  edit(id) {
    UserService.edit(id).then(response => {
      this.setState({
        model: { ...response.data[0] },
        buttontext: "Update",
        videoId: id
      });
    });
  }
  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }
  reset() {
    this.setState({ model: { Url: "", Description: "" }, buttontext: "Save" });
  }
  deleteVideo(id) {
    UserService.deleteVideo(id).then(response => {
      if (response.data.Status)
        this.setState({
          ShowAlertModal: true,
          Message: response.data.Message
        });
      this.allVideos();
    });
  }
  render() {
    const obj = {
      change: this.handleChange,
      save: this.save,
      reset: this.reset,
      CloseModal: this.CloseModal,
      deleteVideo: this.deleteVideo.bind(this),
      edit: this.edit,
      model: this.state
    };
    return <Add {...obj} />;
  }
}



