import React from "react";
import List from "./warehouserequests";
import WarehouseRequestService from "AdminServices/warehouserequest.service.js";
import Index from "./warehouserequests";

export default class WarehouseRequests extends React.Component {
    constructor() {
        super();
        this.state = {
            WarehouseRequests: [],
            total: 0,
            filter: {
                text: "",
                pageNo: 1
            }
        }
    }
    componentWillMount() {
        WarehouseRequestService.GetTotalRequests(this.state.filter).then(response => {
            if (response.data.TotalRecords > 0) {
                let totalRecords = response.data.TotalRecords;
                WarehouseRequestService.GetRequests(this.state.filter).then(res => {
                    this.setState({
                        total: totalRecords,
                        WarehouseRequests: res.data.WarehouseRequests
                    });
                });
            }
        });
    }
    onPageChange(pageNo) {
        const filter = { ...this.state.filter, pageNo: pageNo };
        this.setState({ filter });
        this.getData(filter);
    }

    onSearch(e) {
        e.preventDefault();
        const value = e.target.value;
        const filter = { pageNo: 1, text: value };
        this.getData(filter);
        this.getTotal(filter);
        this.setState({ filter });
    }
    getData(data) {
        WarehouseRequestService.GetRequests(data).then(response => {
            this.setState({
                WarehouseRequests: response.data.WarehouseRequests
            });
        });
    }
    getTotal(data) {
        WarehouseRequestService.GetTotalRequests(data).then(response => {
            this.setState({
                total: response.data.TotalRecords
            });
        });
    }
    render() {
        const model = {
            data: this.state,
            onChange: this.onPageChange.bind(this),
            onSearch: this.onSearch.bind(this)
        }
        return <Index {...model} />;
    }
}



