import React from "react";
import VInput from "Components/common/VInput";
import { Link } from "react-router-dom";
import Pagination from "react-js-pagination";
import Service from "../../../adminsrc/helpers/utility.helper.js";
const list = props => {
    const Requests = props.data.WarehouseRequests.map((val, key) => {
        return (
            <tr key={key}>
                <td>
                    <p>{val.Name}</p>
                </td>
                <td> {val.Email}</td>
                <td> {val.Phone}</td>
                <td> {val.Subject}</td>
                <td>{Service.ConvertDateToString(val.CreatedDate)}</td>
                <td>
                    <Link
                        to={"/admin/request/" + val._id}
                        class="btn btn-success"
                    >
                        Show Detail
                    </Link>
                </td>
            </tr>
        );
    });
    return (
        <div class="container-fluid">
            <div class="breadcrumb-wrapper">
                <div class="container-fluid">
                    <div class="row">
                        <div class="col-md-7 col-sm-7" />
                        <div class="col-md-5 col-sm-5 align-right">
                            <div class="search-bar">
                                <input
                                    type="search"
                                    alt=""
                                    value={props.data.filter.text}
                                    class="search-input"
                                    placeholder="Search"
                                    onChange={props.onSearch}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row margin-top-8x margin-bottom-8x align-center">
                <div class="col-lg-12">
                    <div class="table-div">
                        <div class="table-responsive">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>Name </th>
                                        <th>Email</th>
                                        <th>Phone Number</th>
                                        <th>Subject</th>
                                        <th>Created Date</th>
                                        <th>Detail</th>
                                    </tr>
                                </thead>
                                <tbody>{Requests}</tbody>
                            </table>
                        </div>
                        <div>
                            <Pagination
                                hideDisabled
                                activePage={props.data.filter.pageNo}
                                itemsCountPerPage={10}
                                totalItemsCount={props.data.total}
                                pageRangeDisplayed={5}
                                onChange={props.onChange}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
module.exports = list;



