import React from "react";
import Pagination from "react-js-pagination";
import Helper from "../../helpers/utility.helper.js";
import { Link } from "react-router-dom";
import { TextArea } from "valuelink/tags";
import VInput from "Components/common/VInput";
const Detail = props => {
    let PaymentStatus = "";
    const ALertModal = {
        ModalCss: props.ShowAlertModal ? "in" : "",
        ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
        ModalBody: props.ModalBody,
        onClose: props.onClose,
        Status: props.Payment.UpdatedStatus == 1 ? "Approve" : "Reject"
    };
    const SuccessModal = {
        ModalCss: props.SuccessModal ? "in" : "",
        ModalStyle: { display: props.SuccessModal ? "block" : "none" },
        ModalBody: props.ModalBody,
        onClose: props.onClose
    };
    if (props.Payment) {
        PaymentStatus = props.Payment.SubscriptionRequest.Status == 0 ?
            (<div><button
                class="btn btn-success" onClick={props.goBack}
            >
                Back
            </button>
                &nbsp;&nbsp;
                <button
                    class="btn btn-danger" onClick={() => props.showConfirmation(2)}
                >
                    Reject
                </button></div>) :
            (<span
                class="btn btn-danger"
            >
                Rejected
                </span>)
    }

    return (
        <div class="container-fluid">
            <div class="row margin-top-8x margin-bottom-8x align-center warehouse-dtl">
                <div class="col-lg-12">
                    <h3>
                        Subscription Detail
                    </h3>
                    <div class="form-wrap margin-top-6x">
                        <div class="form-group align-left">
                            <label>Company Name</label>
                            <p>{props.Payment.SubscriptionRequest.Subscriber.FirstName + " " + props.Payment.SubscriptionRequest.Subscriber.LastName}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Email</label>
                            <p>{props.Payment.SubscriptionRequest.Subscriber.Email}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Amount</label>
                            <p>&pound; {props.Payment.SubscriptionRequest.Price}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Mode of Payment</label>
                            <p>{props.Payment.SubscriptionRequest.Method}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Refrence No.</label>
                            <p>{props.Payment.SubscriptionRequest.PaymentId}</p>
                        </div>
                        <div class="form-group align-left">
                            <label>Payment Date</label>
                            <p>{Helper.ConvertDateToString(props.Payment.SubscriptionRequest.CreateDate)}</p>
                        </div>
                        <div class="form-group align-left">
                            {PaymentStatus}
                        </div>
                    </div>
                </div>
            </div>
            <div
                id="myModal"
                class={"modal fade" + ALertModal.ModalCss + "nc-form-decline"}
                role="dialog"
                style={ALertModal.ModalStyle}
            >
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-body align-center">
                            <h3 class="margin-bottom-4x">
                                Are you sure you want to {ALertModal.Status} ?
                            </h3>
                            <div class="form-group margin-top-6x">
                                <label class="pull-left">Reason</label>
                                <TextArea
                                    class="form-control"
                                    name="Description"
                                    valueLink={props.links.reasonLink}
                                />
                                {props.links.reasonLink.error &&
                                    <p class="text-danger">
                                        {props.links.reasonLink.error}
                                    </p>}
                            </div>
                            <div class="form-group margin-top-6x">

                                <input
                                    type="submit"
                                    alt=""
                                    onClick={props.Update}
                                    value="Yes"
                                    class="popup-btn"
                                />
                                <input
                                    type="submit"
                                    alt=""
                                    value="No"
                                    onClick={ALertModal.onClose}
                                    class="popup-btn"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div
                class={"modal fade " + SuccessModal.ModalCss}
                id="myModal-ALert"
                tabIndex="-1"
                role="dialog"
                aria-labelledby="myModalLabel-Alert"
                style={SuccessModal.ModalStyle}
            >
                <div class="modal-dialog" role="document">
                    <div class="modal-content forgot">
                        <div class="closewrap">
                            <button
                                type="button"
                                class="close"
                                data-dismiss="modal"
                                onClick={SuccessModal.onClose}
                            >
                                &times;
              </button>
                        </div>
                        <div class="modal-body forgot-password">
                            <div class="headpassword">
                                <h2></h2>
                                <p>{SuccessModal.ModalBody}</p>
                            </div>

                            <div class="formWarp password">
                                <div class="form-group">
                                    <Link
                                        to="/admin/subscriptions"
                                        class="popup-btn"
                                    >
                                        Ok
                  </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    );
};
module.exports = Detail;



