import React from "react";
import Pagination from "react-js-pagination";
const newsLetter = props => {
  const data = props.model.result.map((item, key) => {
    return (
      <tr key={key}>
        <td>
          {key + 1}
        </td>
        <td>
          {item.email_address}
        </td>
      </tr>
    );
  });
  return (
    <div class="container-fluid">
      <div class="col-lg-12">
        <h3 class="margin-bottom-6x">Manage Newsletter</h3>
      </div>
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <div class="table-div">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Sr no.</th>
                    <th>Email</th>
                  </tr>
                </thead>
                <tbody>
                  {data}
                </tbody>
              </table>
            </div>
            <div>
              <Pagination
                hideDisabled
                activePage={props.model.pageNo}
                itemsCountPerPage={10}
                totalItemsCount={props.model.total}
                pageRangeDisplayed={10}
                onChange={props.getMembers}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = newsLetter;



