import React, { Component } from "react";
import List from "./list.js";
import Service from "AdminServices/testimonial.service.js";
import Link from "valuelink";

const model = {
  Name: "",
  Description: "",
  Image: ""
};
let links = {};
export default class listContainer extends Component {
  constructor() {
    super();

    this.state = {
      list: [],
      model: { ...model },
      pageNo: 1,
      total: 0
    };
    this.getAll = this.getAll.bind(this);
    this.save = this.save.bind(this);
    this.cancel = this.cancel.bind(this);
    this.onUploadimage = this.onUploadimage.bind(this);
    this.onchange = this.onchange.bind(this);
    this.delete = this.delete.bind(this);
  }

  componentDidMount() {
    this.getAll(1);
  }
  onchange(e) {
    e.preventDefault();
    const { name, value } = e.target;
    const model = { ...this.state.model, [name]: value };
    this.setState({ model });
  }
  getAll(pageNo) {
    Service.All()
      .then(response => {
        this.setState({ list: response.data });
      })
      .catch(err => {
        this.setState({ list: [] });
        console.log(err);
      });
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const hasParent = !!errorObjects[0].parent;
      if (hasParent) {
        const parent = errorObjects[0].parent.key,
          key = errorObjects[0].key;
        const obj = this.state[parent];
        obj[key] = undefined;
        this.setState({ [parent]: obj });
      } else {
        const key = errorObjects[0].key;
        //.map(x => x.key)[0];
        this.setState({ [key]: undefined });
      }

      return false;
    } else return true;
  }
  save() {
    const data = this.state.model;
    if (this.valid() && data.Image && data.Image != "")
      Service.Save(data)
        .then(response => {
          this.getAll(1);
          this.setState({ model: { ...model } });
          document.getElementById("removeImage").click();
        })
        .catch(err => {
          console.log(err);
        });
  }

  cancel() {
    document.getElementById("removeImage").click();
    this.setState({ model: { ...model } });
  }
  delete(id) {
    Service.Delete(id)
      .then(response => {
        this.getAll(this.state.pageNo);
      })
      .catch(err => {
        // console.log(err);
      });
  }

  onUploadimage(e) {
    e.preventDefault();
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif")
    ) {
      formData.append("image", files[0]);
      formData.append("name", name);
      Service.UploadImage(formData)
        .then(response => {
          const Image = response.data;

          this.setState({
            model: {
              ...this.state.model,
              Image
            }
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }

  render() {
    links = {
      nameLink: Link.state(this, "model")
        .at("Name")
        .check(x => x, "Name Required"),

      descLink: Link.state(this, "model")
        .at("Description")
        .check(x => x, "Description Required")
    };

    const data = {
      data: this.state,
      links,
      save: this.save,
      cancel: this.cancel,
      onUploadimage: this.onUploadimage,
      onchange: this.onchange,
      delete: this.delete
    };
    return <List {...data} />;
  }
}



