import React from "react";
import List from "./managecontactus.js";
import MessageService from "AdminServices/contact.service.js";

export default class ManageContactUs extends React.Component {
  constructor() {
    super();
    this.state = {
      activePage: 1,
      totalCount: 0,
      messageList: {},
      Message: {},
      ShowAlertModal: false,
      ModalBody: "",
      SuccessModal: false,
      errorModal: false,
      MessageDetail: "",
      ShowMessageModal: false
    };
  }
  handlePageChange(pageNumber) {
    this.getMessage(pageNumber);
  }
  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      SuccessModal: false,
      errorModal: false,
      ShowMessageModal: false,
      ModalBody: ""
    });
    this.reset();
  }
  getMessage(page) {
    MessageService.All({
      pageNo: page
    }).then(response => {
      this.setState({
        messageList: response.data.messages,
        activePage: page,
        totalCount: response.data.Count
      });
    });
  }
  componentWillMount() {
    const skipRecord = 0;
    this.getMessage(1);
  }
  removeMessage(message) {
    this.setState({ Message: message, ShowAlertModal: true });
  }
  viewMessage(message) {
    this.setState({ MessageDetail: message.Message, ShowMessageModal: true });
  }
  DeleteMessage() {
    let index = this.state.messageList.indexOf(this.state.Message);
    MessageService.RemoveMessage(this.state.Message._id).then(response => {
      if (response.data.status) {
        var Messages = this.state.messageList;
        Messages.splice(index, 1);
        this.setState({
          messageList: Messages,
          Message: {},
          SuccessModal: true,
          ShowAlertModal: false,
          errorModal: false,
          ShowMessageModal: false,
          ModalBody: response.data.Message
        });
      } else {
        this.setState({
          errorModal: true,
          ModalBody: "Server Error,Please try again"
        });
      }
    });
  }
  render() {
    const Messages = {
      model: this.state,
      handlePageChange: this.handlePageChange.bind(this),
      DeleteMessage: this.DeleteMessage.bind(this),
      RemoveMessage: this.removeMessage.bind(this),
      SuccessModal: this.state.SuccessModal,
      ShowAlertModal: this.state.ShowAlertModal,
      ShowMessageModal: this.state.ShowMessageModal,
      ModalBody: this.state.ModalBody,
      onClose: this.CloseModal.bind(this),
      viewMessage: this.viewMessage.bind(this)
    };
    return <List {...Messages} />;
  }
}



