import React from "react";
import Add from "./add.js";
import Link from "valuelink";
import UserService from "AdminServices/admin.service.js";
import BlogService from "AdminServices/blog.service.js";
import TextEditor from "react-rte";
let links = {};

export default class AdminBlog extends React.Component {
  constructor() {
    super();
    this.state = {
      BlogImage: "",
      Name: "",
      By: "",
      Description: "",
      ShowAlertModal: false,
      SuccessModal: false,
      ModalBody: "",
      Content: TextEditor.createEmptyValue()
    };
    this.UploadImage = this.UploadImage.bind(this);
  }
  UploadImage(e) {
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif") &&
      !files[0].type.includes("svg")
    ) {
      formData.append("image", files[0]);

      UserService.UploadImage(formData)
        .then(response => {
          $(".thumbnail")[0].src = response.data;
          this.setState({
            BlogImage: response.data
          });
        })
        .catch(err => {
        });
    } else {
      document.getElementById("blogImage").value = "";
    }
  }
  handleChange(value) {
    this.setState({ Content: value });
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  RemoveImage() {}
  saveBlog() {
    if (this.state.BlogImage == "") {
      this.setState({
        ShowAlertModal: true,
        ModalBody: "Please Upload Image"
      });
      return false;
    }
    var newContent = this.state.Content.toString("html");
    if (newContent == "" || newContent == undefined) {
      this.setState({
        ShowAlertModal: true,
        ModalBody: "Please enter Description"
      });
      return false;
    }
    if (this.valid()) {
      var BlogData = { ...this.state };
      BlogData.Description = newContent;      
      BlogData.UrlDescription = this.state.Name.trim().replace(/ /g,"-");      
      BlogService.AddBlog(BlogData).then(response => {
        this.setState({
          SuccessModal: true,
          ModalBody: "Blog added succesfully"
        });
      });
    }
  }
  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      ModalBody: ""
    });
  }
  render() {
    links = {
      blogNameLink: Link.state(this, "Name").check(
        x => x,
        "Blog Name is required"
      ),

      bylink: Link.state(this, "By").check(x => x, "By is required")
    };
    const data = {
      UploadImage: this.UploadImage,
      RemoveImage: this.RemoveImage.bind(this),
      saveBlog: this.saveBlog.bind(this),
      ShowAlertModal: this.state.ShowAlertModal,
      SuccessModal: this.state.SuccessModal,
      ModalBody: this.state.ModalBody,
      onClose: this.CloseModal.bind(this),
      editorChange: this.handleChange.bind(this),
      State: this.state,
      links
    };
    return <Add {...data} />;
  }
}



