import React from "react";
import Detail from "./detail.js";
import BlogService from "AdminServices/blog.service.js";
export default class BlogDetail extends React.Component {
  constructor() {
    super();
    this.state = {
      blogObj: {}
    };
  }
  componentWillMount() {
    const blogId = this.props.match.params.id;
    BlogService.getBlog(blogId).then(response => {
      this.setState({ blogObj: response.data });
    });
  }
  deleteBlog(Id) {
    BlogService.deleteBlog(Id).then(response => {
      if (response.data.Status) {
        this.props.history.push("/admin/blogs");
      }
    });
  }
  render() {
    const obj = {
      model: this.state,
      deleteBlog: this.deleteBlog.bind(this)
    };
    return <Detail {...obj} />;
  }
}



