import React from "react";
import List from "./list.js";
import Service from "AdminServices/customers.service.js";
import WareService from "AdminServices/warehouse.service.js";
let skip = 1;
export default class WarehouseProvider extends React.Component {
  constructor() {
    super();
    this.state = { activePage: 1, totalCount: 0, providerList: {}, text: "" };
    this.changeStatus = this.changeStatus.bind(this);
    this.onSearch = this.onSearch.bind(this);
  }
  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber });
    let skipCount = (pageNumber - skip) * 10;
    this.providerList(this.state.text, skipCount);
  }

  changeStatus(userId, status) {
    Service.UpdateStatus({ id: userId, status: status })
      .then(response => {
        const providerList = this.state.providerList;
        const user = providerList.find(x => x._id.userId == userId);
        const index = providerList.indexOf(user);
        user._id.userStatus = status;
        providerList[index] = user;

        this.setState({ providerList });
      })
      .catch(err => {
        console.log(err);
      });
  }
  providerList(text, skipRecord) {
    WareService.NoofWarehouses({
      text: text,
      pageNo: skipRecord
    }).then(response => {
      this.setState({ providerList: response.data });
    });

    WareService.Warehousecount({ text: text }).then(response => {
      this.setState({ totalCount: response.data.length });
    });
  }
  onSearch(e) {
    e.preventDefault();
    const value = e.target.value;
    const text = value;

    this.setState({
      text,
      activePage: 0
    });
    this.providerList(text, 0);
  }
  componentWillMount() {
    const skipRecord = 0;
    this.providerList("", skipRecord);
  }

  render() {
    const wareList = {
      model: this.state,
      changeStatus: this.changeStatus,
      handlePageChange: this.handlePageChange.bind(this),
      onSearch: this.onSearch
    };
    return <List {...wareList} />;
  }
}



