import React from "react";
import Index from "./detail.js";
import SubscriptionService from "AdminServices/subscription.service.js";
import Link from "valuelink";
let links = {};
export default class SubscriptionDetailContainer extends React.Component {
    constructor() {
        super();
        this.state = {
            SubscriptionRequest: {
                Price: 0,
                PaymentId: "",
                CreateDate: new Date(),
                Status: 0,
                Subscriber: {
                    FirstName: "",
                    LastName: "",
                    Email: ""
                },
                Reason: ""
            },

            UpdatedStatus: 0,
            ShowAlertModal: false,
            SuccessModal: false,
            ModalBody: "",
        }
    }
    componentWillMount() {
        const requestId = this.props.match.params.id;
        SubscriptionService.GetSubscriptionDetail(requestId).then(response => {
            this.setState({
                SubscriptionRequest: response.data[0]
            });
        });
    }
    CloseModal() {
        this.setState({
            ShowAlertModal: false,
            SuccessModal: false,
            ModalBody: "",
            UpdatedStatus: 0
        });
    }
    ConfirmationAlert(status) {
        this.setState({ ShowAlertModal: true, UpdatedStatus: status });
    }
    onChange(e) {
        const { name, value } = e.target;
        this.setState({ Reason: value });
    }
    updateStatus() {
        if (this.valid()) {
            let subscription = { ...this.state.SubscriptionRequest };
            subscription.Status = this.state.UpdatedStatus;
            SubscriptionService.Update(subscription).then(response => {
                if (response.data.Status) {
                    this.setState({
                        ShowAlertModal: false,
                        SuccessModal: true,
                        ModalBody: response.data.Message
                    })
                }
            });
        }
    }
    goBack() {
        this.props.history.goBack();
    }
    valid() {
        const errorObjects = Object.values(links).filter(x => !!x.error);
        if (errorObjects.length) {
            const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
            if (key) {
                this.setState({ [key]: undefined });
            }
            return false;
        } else return true;
    }
    render() {
        links = {
            reasonLink: Link.state(this, "SubscriptionRequest").at("Reason")
                .check(x => x, "Reason is required")
        }
        const model = {
            Payment: this.state,
            Update: this.updateStatus.bind(this),
            ShowAlertModal: this.state.ShowAlertModal,
            SuccessModal: this.state.SuccessModal,
            ModalBody: this.state.ModalBody,
            showConfirmation: this.ConfirmationAlert.bind(this),
            onClose: this.CloseModal.bind(this),
            links,
            goBack: this.goBack.bind(this)
        }
        return <Index {...model} />;
    }
}



