import React from "react";
import VInput from "Components/common/VInput";
import { Link } from "react-router-dom";
import Pagination from "react-js-pagination";
import Service from "../../../adminsrc/helpers/utility.helper.js";
const list = props => {
    const Payments = props.data.SubscriptionRequests.map((val, key) => {
        const PaymentStatus = val.Status == 0 ? "Pending" : val.Status == 1 ? "Approved" : "Rejected";
        return (
            <tr key={key}>
                <td>
                    <p>{val.Subscriber.FirstName + " " + val.Subscriber.LastName}</p>
                </td>
                <td> {val.Subscriber.Email}</td>
                <td> {val.Method}</td>
                <td>{Service.ConvertDateToString(val.CreateDate)}</td>
                <td>
                    <Link
                        to={"/admin/subscription/" + val._id}
                        class="btn btn-success"
                    >
                        Detail
                    </Link>
                    &nbsp;
                    <button
                        class="btn btn-danger"
                    >
                        {PaymentStatus}
                    </button>
                </td>
            </tr>
        );
    });
    return (
        <div class="container-fluid">
            <div class="breadcrumb-wrapper">
                <div class="container-fluid">
                    <div class="row">
                        <div class="col-md-7 col-sm-7" />
                        <div class="col-md-5 col-sm-5 align-right">
                            <div class="search-bar">
                                <input
                                    type="search"
                                    alt=""
                                    value={props.data.filter.text}
                                    class="search-input"
                                    placeholder="Search"
                                    onChange={props.onSearch}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row margin-top-8x margin-bottom-8x align-center">
                <div class="col-lg-12">
                    <div class="table-div">
                        <div class="table-responsive">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>Name </th>
                                        <th>Email</th>
                                        <th>Mode of Payment</th>
                                        <th>Payment Date</th>
                                        <th>Status</th>
                                    </tr>
                                </thead>
                                <tbody>{Payments}</tbody>
                            </table>
                        </div>
                        <div>
                            <Pagination
                                hideDisabled
                                activePage={props.data.filter.pageNo}
                                itemsCountPerPage={10}
                                totalItemsCount={props.data.total}
                                pageRangeDisplayed={5}
                                onChange={props.onChange}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
module.exports = list;



