import React from "react";
import Profile from "./profile.js";
import WareService from "AdminServices/warehouse.service.js";
import UserService from "AdminServices/admin.service.js";
import Helper from "Helpers/utility.helper.js";
let skip = 1;
export default class ProviderProfile extends React.Component {
  constructor() {
    super();
    this.state = {
      activePage: 1,
      totalCount: 0,
      userWareList: {},
      userInfo: {},
      userRoleList: {}
    };
  }
  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber });
    let skipCount = (pageNumber - skip) * 10;
    this.providerList(skipCount);
  }

  providerList(skipCount) {
    const userId = this.props.match.params.id;
    WareService.providList(skipCount, userId).then(response => {
      this.setState({ userWareList: response.data });
    });
  }

  componentWillMount() {
    const userId = this.props.match.params.id;
    UserService.getUserInformation(userId).then(response => {
      this.setState({ userInfo: response.data });
    });

    UserService.getUserRoleList(userId).then(response => {
      this.setState({ userRoleList: response.data });
    });
    const skipRecord = 0;
    this.providerList(skipRecord);

    WareService.allProviderInfo(userId).then(response => {
      this.setState({ totalCount: response.data.length });
    });
  }

  render() {
    const wareObj = {
      model: this.state,
      handlePageChange: this.handlePageChange.bind(this)
    };
    return <Profile {...wareObj} />;
  }
}



