import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import List from "./list.js";
import AdminService from "AdminServices/admin.service.js";
import Link from "valuelink";

let links = {};
export default class FAQ extends React.Component {
  constructor() {
    super();
    this.state = {
      Title: "",
      Description: "",
      faqList: {},
      faqId: "",
      name: "Add",
      buttonText: "Save",
      showAddFaq: false,
      ShowAlertModal: false,
      Message: ""
    };
    this.handleChange = this.handleChange.bind(this);
    this.Save = this.Save.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.reset = this.reset.bind(this);
  }

  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }
  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }
  Save() {
    if (this.state.Description == "") {
      this.setState({
        ShowAlertModal: true,
        Message: "please enter a description"
      });
    }
    if (this.state.Title == "") {
      this.setState({
        ShowAlertModal: true,
        Message: "please enter a title"
      });
    }
    if (this.state.Title != "" && this.state.Description != "") {
      AdminService.saveFaq(this.state).then(response => {
        this.setState({ showAddFaq: false });
        this.getall();
      });
    }
  }

  reset() {
    this.setState({ showAddFaq: false });
  }
  EditFaq(id) {
    AdminService.Editfaq(id).then(response => {
      this.setState({
        Title: response.data[0].Title,
        Description: response.data[0].Description,
        faqId: id,
        showAddFaq: true,
        buttonText: "Update",
        name: "Edit"
      });
      window.scrollTo(0, 0);
    });
  }

  publish(item, ispublish) {
    let FaqList = this.state.faqList;
    let index = FaqList.indexOf(item);
    item.Ispublish = ispublish;
    FaqList[index] = item;
    AdminService.isFaqPublish(item._id, ispublish).then(response => {
      this.setState({
        ShowAlertModal: true,
        Message: response.data.Message,
        faqList: FaqList
      });
    });
  }
  showAdd() {
    this.setState({
      faqId: "",
      buttonText: "Save",
      name: "Add",
      Title: "",
      Description: "",
      showAddFaq: true
    });
    window.scrollTo(0, 0);
  }
  getall() {
    const skipRecord = 0;
    AdminService.getAllFaq(skipRecord).then(response => {
      this.setState({
        faqList: response.data
      });
    });
  }
  deleteFaq(id) {
    AdminService.deleteFaq(id).then(response => {
      this.setState({
        ShowAlertModal: true,
        Message: response.data.Message
      });
      this.getall();
    });
  }
  componentWillMount() {
    if (this.props.location.pathname == "/admin/faq") {
      this.setState({ showAddFaq: false });
      this.getall();
    }
    if (this.props.location.pathname == "/admin/addfaq") {
      this.setState({
        faqId: "",
        buttonText: "Save",
        name: "Add",
        Title: "",
        Description: "",
        showAddFaq: true
      });
    }
    if (this.props.location.pathname.indexOf("/admin/editfaq/") > -1) {
      AdminService.Editfaq(this.props.match.params.id).then(response => {
        this.setState({
          Title: response.data[0].Title,
          Description: response.data[0].Description,
          faqId: this.props.match.params.id,
          showAddFaq: true,
          buttonText: "Update",
          name: "Edit"
        });
        window.scrollTo(0, 0);
      });
    }
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  render() {
    links = {
      titleLink: Link.state(this, "Title").check(x => x, "Title is required"),

      descriptionLink: Link.state(this, "Description").check(
        x => x,
        "Description is required"
      )
    };
    const faqobj = {
      Save: this.Save,
      onchange: this.handleChange,
      EditFaq: this.EditFaq.bind(this),
      deleteFaq: this.deleteFaq.bind(this),
      publish: this.publish.bind(this),
      showAdd: this.showAdd.bind(this),
      model: this.state,
      CloseModal: this.CloseModal,
      reset: this.reset,
      links
    };
    return <List {...faqobj} />;
  }
}



