import React from "react";
import Login from "./login.js";
import Link from "valuelink";
import UserService from "AdminServices/admin.service.js";
import { Set } from "AdminHelpers/axios.helper.js";

const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
let links = null;
export default class adminLogin extends React.Component {
  constructor() {
    super();
    this.onSubmit = this.onSubmit.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.showForgot = this.showForgot.bind(this);
    this.ForgotSubmit = this.ForgotSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      Email: "",
      Password: "",
      Forgotemail: "",
      ShowAlertModal: false,
      Message: "",
      showLogin: true
    };
  }
  componentWillMount() {
    if (this.props.location.pathname == "/admin/login")
      this.setState({ showLogin: true });
    else this.setState({ showLogin: false });
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  onSubmit() {
    if (this.valid()) {
      var data = {
        email: this.state.Email,
        password: this.state.Password
      };
      UserService.login(data).then(response => {
        if (response.data.Status) {
          localStorage.setItem("userinfo", response.data.accessToken);
          Set(response.data.accessToken);
          this.props.history.push("/admin/dashboard");
        } else {
          this.setState({
            ShowAlertModal: true,
            Message: response.data.Message
          });
        }
      });
    }
  }
  ForgotSubmit() {
    UserService.forgotPassword(this.state).then(response => {
      if (response.data.Status) {
        this.setState({
          ShowAlertModal: true,
          Message: response.data.Message
        });
      } else {
        this.setState({
          ShowAlertModal: true,
          Message: response.data.Message
        });
      }
    });
  }
  showForgot(type) {
    if (type == "showLogin") {
      this.setState({ showLogin: true });
      this.props.history.push("/admin/login");
    } else {
      this.setState({ showLogin: false });
      this.props.history.push("/admin/forgotpassword");
    }
  }
  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }
  handleKeyPress(e) {
    if (e.key === "Enter") {
      this.onSubmit();
    }
  }
  render() {
    links = {
      emialLink: Link.state(this, "Email")
        .check(x => x, "Email Required")
        .check(x => EmailRegex.test(x), "Email not valid"),
      passwordLink: Link.state(this, "Password").check(
        x => x,
        "Password Required"
      )
    };
    const loginObj = {
      links,
      onKeyPress: this.handleKeyPress.bind(this),
      onClick: this.onSubmit,
      CloseModal: this.CloseModal,
      showForgot: this.showForgot,
      ForgotSubmit: this.ForgotSubmit,
      handleChange: this.handleChange,
      Model: this.state
    };
    return <Login {...loginObj} />;
  }
}



