import React from "react";
import Index from "./subscriptionPayments.js";
import SubscriptionService from "AdminServices/subscription.service.js";

export default class SubscriptionContainer extends React.Component {
    constructor() {
        super();
        this.state = {
            SubscriptionRequests: [],
            total: 0,
            filter: {
                text: "",
                pageNo: 1
            }
        }
    }
    componentWillMount() {
        SubscriptionService.GetTotalRequests(this.state.filter).then(response => {
            if (response.data.TotalRecords > 0) {
                let totalRecords = response.data.TotalRecords;
                SubscriptionService.GetSubscriptionRequests(this.state.filter).then(subscritions => {
                    this.setState({
                        total: totalRecords,
                        SubscriptionRequests: subscritions.data.Subscriptions
                    });
                });
            }
        });
    }
    onPageChange(pageNo) {
        const filter = { ...this.state.filter, pageNo: pageNo };
        this.setState({ filter });
        this.getData(filter);
    }

    onSearch(e) {
        e.preventDefault();
        const value = e.target.value;
        const filter = { pageNo: 1, text: value };
        this.getData(filter);
        this.getTotal(filter);
        this.setState({ filter });
    }
    getData(data) {
        SubscriptionService.GetSubscriptionRequests(data).then(response => {
            this.setState({
                SubscriptionRequests: response.data.Subscriptions
            });
        });
    }
    getTotal(data) {
        SubscriptionService.GetTotalRequests(data).then(response => {
            this.setState({
                total: response.data.TotalRecords
            });
        });
    }
    render() {
        const model = {
            data: this.state,
            onChange: this.onPageChange.bind(this),
            onSearch: this.onSearch.bind(this)
        }
        return <Index {...model} />;
    }
}



