import React from "react";

const dashboard = props => {
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-md-6">
          <div class="content-wrap">
            <h3 class="margin-bottom-4x">
              Total Numbers of Warehouse providers
            </h3>
            <p>
              ( {props.warehouseCount} )
            </p>
            <a href="/admin/provider">
              <span class="big-icon margin-top-4x">
                <i class="fa fa-users" aria-hidden="true" />
              </span>
            </a>
          </div>
        </div>
        <div class="col-md-6">
          <div class="content-wrap">
            <h3 class="margin-bottom-4x">Total Number of customers</h3>
            <p>
              ( {props.customerCount} )
            </p>
            <a href="/admin/customers">
              <span class="big-icon margin-top-4x">
                <i class="fa fa-users" aria-hidden="true" />
              </span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = dashboard;



