import React from "react";
import Pagination from "react-js-pagination";
import Rating from "Components/common/starRating.js";
const reviewObj = props => {
  // let WareName = "No";
  let reviewsList = "";
  if (Object.keys(props.model.wareReviewList).length) {
    // WareName = props.model.wareReviewList[0].Name;
    reviewsList = props.model.wareReviewList.map(item => {
      return (
        <div class="row newsletter-list margin-top-8x margin-bottom-4x">
          <div class="col-md-2">
            <img
              src={item.Image}
              onError={e => {
                e.target.src = "/Content/images/warehouse/w1.jpg";
              }}
              alt=""
            />
          </div>
          <div class="col-md-8">
            <p>{item.Description}</p>
            <span class="review-stars margin-top-1x">
              <Rating input={item.Rating} />
            </span>
          </div>
          {item.Rating <= 2 && (
            <div class="col-md-2 align-right">
              <button
                class="btn btn-danger"
                onClick={() => props.deleteReview(item._id)}
              >
                Delete
              </button>
            </div>
          )}
        </div>
      );
    });
  } else {
    reviewsList = (
      <div class="no-result-found" key="">
        <p>No Review Found</p>
      </div>
    );
  }
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <h3 class="margin-bottom-6x">{props.model.WareName} Reviews</h3>
          {reviewsList}
          <div>
            <Pagination
              hideDisabled
              activePage={props.model.activePage}
              itemsCountPerPage={10}
              totalItemsCount={props.model.totalCount}
              pageRangeDisplayed={5}
              onChange={props.handlePageChange}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = reviewObj;



