import React from "react";
import VInput from "Components/common/VInput";
import MessageBox from "../common/messagebox.js";
const setting = props => {
  const alertModel = {
    isShow: props.model.ShowAlertModal,
    Message: props.model.Message,
    onClose: props.CloseModal
  };
  return (
    <div class="container-fluid">
      <MessageBox {...alertModel} />
      <div class="row margin-top-8x margin-bottom-8x align-center warehouse-dtl">
        <div class="col-lg-12">
          <div class="form-wrap margin-top-6x">
            <h3>Reset your Password</h3>
            <div class="form-group align-left margin-top-6x">
              <label>Old Password</label>
              <VInput
                type="password"
                className="form-control"
                name="Password"
                placeholder="Password*"
                maxLength="20"
                valueLink={props.links.passwordLink}
              />
            </div>
            <div class="form-group align-left">
              <label>New Password</label>
              <VInput
                type="password"
                className="form-control"
                name="NewPassword"
                placeholder="New password*"
                valueLink={props.links.newpasswordLink}
              />
            </div>
            <div class="form-group align-left margin-bottom-4x">
              <label>Re-enter Password</label>
              <VInput
                type="password"
                className="form-control"
                name="ConfirmPassword"
                placeholder="Confirm password*"
                valueLink={props.links.confirmPasswordLink}
              />
            </div>
            <div class="form-group align-center setting-page">
              <button onClick={props.save} class="btn btn-success">
                Save
              </button>
              <button onClick={props.Reset} class="btn btn-default">
                Cancel
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = setting;



