import React from "react";
import RichTextEditor from "AdminComponents/common/Editor.js";
import Messagebox from "AdminComponents/common/messagebox.js";
const Solution = props => {
  const ALertModal = {
    isShow: props.State.ShowAlertModal,
    onClose: props.CloseModal,
    Message: props.State.Message
  };
  return (
    <div class="container-fluid">
      <Messagebox {...ALertModal} />
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <h3>
            {props.Title}
          </h3>
          <div class=" margin-top-4x margin-bottom-4x align-center">
            <div id="solutionImage" class="imageupload panel panel-default">
              <div class="panel-heading clearfix">
                <h3 class="panel-title pull-left">Upload Image</h3>
              </div>
              <div class="file-tab panel-body">
                <label class="btn btn-success btn-file">
                  <span>Browse</span>

                  <input
                    type="file"
                    name="image-file"
                    onChange={props.uploadImage}
                  />
                </label>
                <button
                  type="button"
                  class="btn btn-danger"
                  onClick={props.removeImage}
                >
                  Remove
                </button>
              </div>
            </div>
            <div class="full-width">
              <div class="form-wrap news-form margin-top-6x">
                <div class="form-group">
                  <label>Enter description</label>
                  {/* <textarea rows="" cols="" class="form-control" /> */}
                  <RichTextEditor {...props} />
                </div>
              </div>
            </div>
          </div>
          <p />
          <div
            dangerouslySetInnerHTML={{
              __html: props.State.Content.toString("html")
            }}
          />
          <div class="form-group align-center setting-page margin-top-6x">
            <button class="btn btn-default" onClick={props.Update}>
              Update
            </button>
            <button class="btn btn-danger" onClick={props.Reset}>
              Reset
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = Solution;



