import React from "react";
import Service from "../../../adminsrc/helpers/utility.helper.js";
import Pagination from "react-js-pagination";

const adminBlogs = props => {
  let listObj = "";
  const ALertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const errorModal = {
    ModalCss: props.errorModal ? "in" : "",
    ModalStyle: { display: props.errorModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const SuccessModal = {
    ModalCss: props.SuccessModal ? "in" : "",
    ModalStyle: { display: props.SuccessModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  if (Object.keys(props.model.adminBlogObj).length) {
    listObj = props.model.adminBlogObj.map(item => {
      let date = Service.ConvertDateToString(item.CreatedDate);
      return (
      
        <li>
          <div class="content-wrap">
            <div class="blog-img">
              <img src={item.BlogImage} alt="" />
            </div>
            <h3 class="margin-bottom-4x">{item.Name}</h3>
            <span class="creater">
              By {item.By} on {date}
            </span>
            <div
              dangerouslySetInnerHTML={{
                __html: item.Description
              }}
            />
            <div class="blog-buttons view-blog-btn">
              <a href={"/admin/blogdetail/" + item._id}>
                <button class="btn btn-success">View</button>
              </a>
              <button
                class="btn btn-danger pull-right"
                onClick={() => props.showDeleteAlert(item)}
              >
                Delete
              </button>
            </div>
          </div>
        </li>
      
      );
    });
  }
  return (
    <div class="container-fluid">
      <div class="align-right">
        <a href="/admin/addblog">
          <button class="btn btn-large btn-success margin-top-6x">
            Create New Blog
          </button>
        </a>
      </div>

      <div class=" margin-top-8x margin-bottom-8x align-center blogs">
        <h3 class="margin-bottom-6x">All Blogs</h3>
        <ul>
        {listObj}
        </ul>
      </div>
      <div>
        <Pagination
          hideDisabled
          activePage={props.model.activePage}
          itemsCountPerPage={10}
          totalItemsCount={props.model.totalCount}
          pageRangeDisplayed={5}
          onChange={props.handlePageChange}
        />
      </div>

      <div
        id="myModal"
        class={"modal fade" + ALertModal.ModalCss + "nc-form-decline"}
        role="dialog"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-body align-center">
              <h3 class="margin-bottom-4x">
                Are you sure you want to Delete this blog?
              </h3>

              <div class="form-group margin-top-6x">
                <div class="input">
                  <input
                    type="submit"
                    alt=""
                    onClick={props.deleteBlog}
                    value="Yes"
                    class="popup-btn"
                  />
                  <input
                    type="submit"
                    alt=""
                    value="No"
                    onClick={ALertModal.onClose}
                    class="popup-btn"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>{SuccessModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <button
                    href="javascript:void(0)"
                    class="popup-btn"
                    onClick={SuccessModal.onClose}
                  >
                    Ok
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = adminBlogs;



