import React from "react";
import Detail from "./detail.js";
import WarehouseRequestService from "AdminServices/warehouserequest.service";
import Link from "valuelink";
let links = {};
export default class WarehouseRequestDetail extends React.Component {
    constructor() {
        super();
        this.state = {
            WarehouseRequest: {
                Name: "",
                Email: "",
                CreateDate: new Date(),
                Status: 0,
                Warehouse: {
                    Name: "",
                    Address: "",
                    City: ""
                },
                Subject: "",
                Message: "",
                FromDate: "",
                ToDate: "",
                SpaceRequired: ""
            }
        };
    }
    componentWillMount() {
        const requestId = this.props.match.params.id;
        WarehouseRequestService.GetRequestDetail(requestId).then(response => {
            this.setState({
                WarehouseRequest: response.data[0]
            });
        });
    }
    goBack() {
        this.props.history.goBack();
    }
    render() {
        const model = {
            State: this.state.WarehouseRequest,
            goBack: this.goBack.bind(this)
        }
        return <Detail {...model} />;
    }
}



