import React from "react";
import MessageBox from "../common/messagebox.js";
import VInput from "Components/common/VInput";
import { TextArea } from "valuelink/tags";

const Addfaq = props => {
  const alertModel = {
    isShow: props.model.ShowAlertModal,
    Message: props.model.Message,
    onClose: props.CloseModal
  };
  return (
    <div class="row">
      <MessageBox {...alertModel} />
      <div class="col-lg-12">
        <div class="margin-top-4x margin-bottom-4x" />
        <div class="margin-top-4x margin-bottom-4x">
          <div class="form-wrap news-form margin-top-6x">
            <div class="form-group">
              <label>{props.model.name} Title</label>
              <VInput
                type="text"
                name="Title"
                maxLength="100"
                valueLink={props.links.titleLink}
                class="form-control"
              />
            </div>
            <div class="form-group">
              <label>{props.model.name} description</label>
              <TextArea
                class="form-control"
                rows="5"
                id="comment"
                name="Description"
                valueLink={props.links.descriptionLink}
              />
              {props.links.descriptionLink.validationError && (
                <p class="text-danger">
                  {" "}
                  {props.links.descriptionLink.validationError}
                </p>
              )}
            </div>
          </div>
        </div>
        <div class="form-group setting-page margin-top-6x">
          <button class="btn btn-success" onClick={props.Save}>
            {props.model.buttonText}
          </button>
          <a class="btn btn-default" href="/admin/faq">
            Cancel
          </a>
        </div>
      </div>
    </div>
  );
};

module.exports = Addfaq;



