import React from "react";
import Service from "../../../adminsrc/helpers/utility.helper.js";

const detailObj = props => {
  let date = "";
  if (Object.keys(props.model.blogObj).length) {
    date = Service.getFullDateFormat(props.model.blogObj[0].CreatedDate);
  }
  return (
    <div class="container-fluid">
      {Object.keys(props.model.blogObj).length &&
        <div class="row margin-top-8x margin-bottom-8x align-center blogs blog-dtl">
          <div class="col-md-12">
            <div class="content-wrap">
              <div class="blog-img">
                <img src={props.model.blogObj[0].BlogImage} alt="" />
              </div>

              <h3 class="margin-bottom-4x">
                {props.model.blogObj[0].Name}
              </h3>
              <span class="creater">
                By {props.model.blogObj[0].By}
              </span>
              <span class="creater">
                on {date}
              </span>

              <p
                dangerouslySetInnerHTML={{
                  __html: props.model.blogObj[0].Description
                }}
              />
              <div class="blog-buttons">
                <a href={"/admin/editblog/" + props.model.blogObj[0]._id}>
                  <button class="btn btn-default">Edit this Blog</button>
                </a>
                <button
                  class="btn btn-danger"
                  data-toggle="modal"
                  data-target="#myModal"
                  onClick={() => props.deleteBlog(props.model.blogObj[0]._id)}
                >
                  Delete this Blog
                </button>
                <a href="/admin/blogs">
                  <button class="btn btn-default pull-right">Back</button>
                </a>
              </div>
            </div>
          </div>
        </div>}
    </div>
  );
};

module.exports = detailObj;



