import React from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Route,
  Link,
  withRouter,
  Switch
} from "react-router-dom";
import ErrorHandler from "AdminComponents/errorhandler/errorHandler.container.js";
import { BrowserRouter } from "react-router-dom";
import routes from "./adminroute.config.js";
import AdminLogin from "AdminComponents/adminlogin/login.container.js";
import AdminSideHeader from "AdminComponents/admin.sidebar.js";
const App = ({ location }) => {
  const currentRoute = routes.find(x =>
    location.pathname.includes(x.path.split(":")[0])
  );
  const showHeader = currentRoute ? currentRoute.protected : false;
  const routeObject = routes.map((x, i) => {
    return (
      <Route
        key={i}
        exact
        path={x.path}
        onEnter={window.scrollTo(0, 0)}
        render={props => <x.component {...props} />}
      />
    );
  });

  if (showHeader)
    return (
      <AdminSideHeader>
        {" "}
        <Switch>
          {routeObject}
          <Route component={ErrorHandler} />
        </Switch>
      </AdminSideHeader>
    );
  else {
    return (
      <div>
        {" "}
        <Switch>
          {routeObject}
          <Route component={ErrorHandler} />
        </Switch>
      </div>
    );
  }
};
ReactDOM.render(
  <Router>
    <Route render={({ location }) => <App location={location} />} />
  </Router>,
  document.getElementById("root")
);



