import { http } from "AdminHelpers/axios.helper.js";
export default class CutomerService {
  static All(data) {
    const url =
      "/api/user/customers?text=" + data.text + "&pageno=" + data.pageNo;
    return http.get(url);
  }
  static Count(data) {
    const url = "/api/user/customers/count?text=" + data.text;
    return http.get(url);
  }
  static UpdateStatus(data) {
    const url = "/api/user/status?id=" + data.id + "&status=" + data.status;
    return http.get(url);
  }
}



