import JWT from "jwt-decode";

class Helper {
  static DecodeToken(token) {
    try {
      return JWT(token);
    } catch (err) {
      return null;
    }
  }
  static ConvertDateToString(dateString) {
    let date = dateString != null ? new Date(dateString) : new Date();
    return (
      date.getDate() +
      "/" +
      (parseInt(date.getMonth()) + 1) +
      "/" +
      date.getFullYear()
    );
  }
  static ConvertFromDateToString(dateString) {
    let date = dateString != null ? new Date(dateString) : new Date();
    return (
      date.getDate() +
      "/" +
      (parseInt(date.getMonth()) + 1) +
      "/" +
      date.getFullYear()
    );
  }
  static ConvertToDateToString(dateString) {
    if (dateString == null) {
      return "";
    } else {
      let date = dateString != null ? new Date(dateString) : new Date();
      return (
        date.getDate() +
        "/" +
        (parseInt(date.getMonth()) + 1) +
        "/" +
        date.getFullYear()
      );
    }
  }
  static ConvertDateFormat(date) {
    return (
      date.getDate() +
      "/" +
      (parseInt(date.getMonth()) + 1) +
      "/" +
      date.getFullYear()
    );
  }
  static SetEndDate(dateString) {
    let date = dateString != null ? new Date(dateString) : new Date();
    let year = dateString != null ? date.getFullYear() : 2050;
    return date.getDate() + "/" + (parseInt(date.getMonth()) + 1) + "/" + year;
  }
  static ConvertTimeFormat(date) {
    const newdate = new Date(date);
    return (
      newdate.getHours() +
      ":" +
      newdate.getMinutes() +
      ":" +
      newdate.getSeconds()
    );
  }

  static GetDateFromString(dateStr) {
    return new Date(dateStr);
  }
  static GetParamByName(name) {
    const url = window.location.search;
    name = name.replace(/[\[\]]/g, "\\$&");
    const regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
      results = regex.exec(url);
    if (!results) return "";
    if (!results[2]) return "";
    if (results[2] == "undefined") return "";
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  }

  static GetDistanceFromLatLonInMiles(lat1, lon1, lat2, lon2) {
    const R = 6371; // Radius of the earth in km
    const dLat = deg2rad(lat2 - lat1); // deg2rad below
    const dLon = deg2rad(lon2 - lon1);
    const a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(deg2rad(lat1)) *
        Math.cos(deg2rad(lat2)) *
        Math.sin(dLon / 2) *
        Math.sin(dLon / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    const d = R * c; // Distance in km
    const miles = d / 1.60934;
    return miles;
  }
  static getDaysDiffrence(date) {
    const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
    const firstDate = new Date(date);
    const secondDate = new Date();
    const diffDays = Math.round(
      Math.abs((firstDate.getTime() - secondDate.getTime()) / oneDay)
    );
    return diffDays;
  }
  static RoundOffCapacity(num) {
    return Math.ceil(num * 10) / 10;
  }
}
module.exports = Helper;



