import { http } from "AdminHelpers/axios.helper.js";

export default class WarehouseService {
  static NoofWarehouses(data) {
    const url =
      "/api/warehouse/providerlist?text=" +
      data.text +
      "&pageNo=" +
      data.pageNo;
    return http.get(url);
  }
  static Warehousecount(data) {
    const url = "/api/warehouse/providercount?text=" + data.text;
    return http.get(url);
  }
  static UserWarehousesList(pageno, userid) {
    return http.get("/api/warehouse/userWareList", {
      params: {
        pageno: pageno,
        userid: userid
      }
    });
  }
  static alluserwareList(userid) {
    return http.get("/api/warehouse/AlluserWareCount/" + userid);
  }
  static GetWareDetail(wareId) {
    const url = "/api/warehouse/wareDetailById/" + wareId;
    return http.get(url);
  }
  static wareDetailList(pageNo, wareId) {
    const url =
      "/api/warehouse/GetwareDetailList?pageNo=" +
      pageNo +
      "&warehouseId=" +
      wareId;
    return http.get(url);
  }
  static allDetail(wareId) {
    const url = "/api/warehouse/allWareDetailList/" + wareId;
    return http.get(url);
  }
  static providList(pageno, userid) {
    return http.get("/api/warehouse/getproviderList", {
      params: {
        pageno: pageno,
        userid: userid
      }
    });
  }
  static allProviderInfo(userid) {
    var url = "/api/warehouse/allproviderlist/" + userid;
    return http.get(url);
  }
}



