import { http } from "AdminHelpers/axios.helper.js";

export default class testimonialService {
  static All(pageNo) {
    const url = "/api/admin/testimonial/all?pageNo=" + pageNo;
    return http.get(url);
  }
  static Delete(id) {
    const url = "/api/admin/testimonial/delete/" + id;
    return http.get(url);
  }
  static Get(id) {
    const url = "/api/admin/testimonial/get" + id;
    return http.get(url);
  }
  static Save(data) {
    const url = "/api/admin/testimonial/save";
    return http.post(url, data);
  }

  static UploadImage(data) {
    const url = "/api/admin/testimonial/uploadimage";
    return http.post(url, data);
  }
}



