import React from "react";

const listObj = props => {
  let videoList = "";
  if (Object.keys(props.model.videoList).length) {
    videoList = props.model.videoList.map(item => {
      return (
        <div class="row newsletter-list margin-top-8x margin-bottom-4x">
          <div class="col-md-4 align-center">
            <iframe
              width="100%"
              height="300"
              src={item.Url}
              frameBorder="0"
              allowfullscreen
              class=" margin-bottom-2x"
            />
          </div>
          <div class="col-md-6">
            {/*<h3 class="margin-bottom-2x">How to list space</h3>*/}
            <p>{item.Description}</p>
          </div>
          <div class="col-md-2 align-right">
            <button
              class="btn btn-default"
              onClick={() => props.edit(item._id)}
            >
              Edit
            </button>
            <button
              class="btn btn-danger"
              onClick={() => props.deleteVideo(item._id)}
            >
              Delete
            </button>
          </div>
        </div>
      );
    });
  }
  return <div>{videoList}</div>;
};
module.exports = listObj;



