import { http } from "AdminHelpers/axios.helper.js";

export default class BlogService {
  static AddBlog(data) {
    const url = "/api/user/addAdminBlog";
    return http.post(url, data);
  }
  static all() {
    const url = "/api/user/getAllBlogList";
    return http.get(url);
  }
  static getSkipBlockList(skipNo) {
    const url = "/api/user/blockLIstBypage/" + skipNo;
    return http.get(url);
  }
  static deleteBlog(Id) {
    const url = "/api/user/deleteBlogById/" + Id;
    return http.get(url);
  }
  static getBlog(Id) {
    const url = "/api/user/getBlogById/" + Id;
    return http.get(url);
  }
  static updateBlog(blogObj) {
    const url = "/api/user/updateBlog";
    return http.post(url, blogObj);
  }
}



