import React from "react";
import Pagination from "react-js-pagination";
import Helper from "../../helpers/utility.helper.js";
const wareList = props => {
  let listObj = "";
  if (Object.keys(props.model.providerList).length) {
    listObj = props.model.providerList.map((item, key) => {
      if (item._id.Email != "mousumi@ignivasolutions.com") {
        let statusObj = {};
        if (item._id.userStatus == 3) {
          statusObj.class = "red";
          statusObj.name = "Deactivate";
        } else {
          statusObj.class = "green";
          statusObj.name = "Activate";
        }

        return (
          <tr key={key}>
            <td>
              <a href={"/admin/providerprofile/" + item._id.userId}>
                {item._id.Name}
              </a>
            </td>
            <td>
              {item._id.Email}
            </td>
            <td>
              {item._id.totalWareHouse}
            </td>
            <td>
              {Helper.ConvertDateToString(item._id.CreateDate)}
            </td>
            <td>
              <a
                href="javascript:void(0)"
                onClick={() =>
                  props.changeStatus(
                    item._id.userId,
                    item._id.userStatus != 3 ? 3 : 2
                  )}
                class={statusObj.class}
              >
                {statusObj.name}
              </a>
            </td>
          </tr>
        );
      }
    });
  }

  return (
    <div class="container-fluid">
      <div class="breadcrumb-wrapper">
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-7 col-sm-7" />
            <div class="col-md-5 col-sm-5 align-right">
              <div class="search-bar">
                <input
                  type="search"
                  alt=""
                  value={props.model.text}
                  class="search-input"
                  placeholder="Search"
                  onChange={props.onSearch}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <div class="table-div">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Name </th>
                    <th>Email</th>
                    <th>No. of warehouses</th>
                    <th>Created On</th>
                    <th>Deactivate User</th>
                  </tr>
                </thead>
                <tbody>
                  {listObj}
                </tbody>
              </table>
            </div>
            <div>
              <Pagination
                hideDisabled
                activePage={props.model.activePage}
                itemsCountPerPage={10}
                totalItemsCount={props.model.totalCount}
                pageRangeDisplayed={5}
                onChange={props.handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = wareList;



