import { http } from "AdminHelpers/axios.helper.js";

export default class WarehouseRequestService {
    static GetRequests(data) {
        const url = "/api/warehouserequest/all?text=" + data.text + "&pageno=" + data.pageNo;
        return http.get(url);
    }
    static GetTotalRequests(data) {
        const url = "/api/warehouserequest/count?text=" + data.text;
        return http.get(url);
    }
    static GetRequestDetail(data) {
        const url = "/api/warehouserequest/detail/" + data;
        return http.get(url);
    }
}



