import { http } from "AdminHelpers/axios.helper.js";

export default class SubscriptionService {
    static GetSubscriptionRequests(data) {
        const url = "/api/subscription/all?text=" + data.text + "&pageno=" + data.pageNo;
        return http.get(url);
    }
    static GetTotalRequests(data) {
        const url = "/api/subscription/count?text=" + data.text;
        return http.get(url);
    }
    static GetSubscriptionDetail(data) {
        const url = "/api/subscription/detail/" + data;
        return http.get(url);
    }
    static Update(data) {
        const url = "/api/subscription/update";
        return http.post(url, data);
    }
}



