import React from "react";
import Detail from "./detail.js";
import WareService from "AdminServices/warehouse.service.js";
import UserService from "AdminServices/admin.service.js";
import Helper from "Helpers/utility.helper.js";

export default class WareDetail extends React.Component {
  constructor() {
    super();
    this.state = {
      activePage: 1,
      totalCount: 0,
      wareDetail: {},
      Userlist: {}
    };
  }
  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber });
    let skipCount = (pageNumber - skip) * 10;
    this.wareDetailList(skipCount);
  }

  wareDetailList(skipCount) {
    const warehouseId = this.props.match.params.id;
    WareService.wareDetailList(skipCount, warehouseId).then(response => {
      this.setState({ Userlist: response.data });
    });
  }

  componentWillMount() {
    const skipRecord = 0;
    const warehouseId = this.props.match.params.id;
    this.wareDetailList(skipRecord);
    WareService.GetWareDetail(warehouseId).then(response => {
      this.setState({ wareDetail: response.data });
    });
    WareService.allDetail(warehouseId).then(response => {
      this.setState({ totalCount: response.data.length });
    });
  }
  render() {
    const UserWareObj = {
      model: this.state,
      handlePageChange: this.handlePageChange.bind(this)
    };
    return <Detail {...UserWareObj} />;
  }
}



