import React from "react";
import Pagination from "react-js-pagination";
import Service from "../../../adminsrc/helpers/utility.helper.js";
const customers = props => {
  const data = props.data.result.map((item, key) => {
    if (item._id.Email != "mousumi@ignivasolutions.com") {
      //admin EmailId
      return (
        <tr key={key}>
          <td>
            <a href={"/admin/warehouses/" + item._id.userId}>{item._id.Name}</a>
          </td>
          <td> {item._id.Email}</td>
          <td>{item._id.totalWareHouse}</td>
          <td>{Service.ConvertDateToString(item._id.date)}</td>
          <td>
            <a
              href="javascript:void(0)"
              class={item._id.Status != 3 ? "green" : "red"}
              onClick={() => {
                props.changeStatus(
                  item._id.userId,
                  item._id.Status != 3 ? 3 : 2
                );
              }}
            >
              {item._id.Status != 3 ? "Activate" : "De-Activate"}
            </a>
          </td>
        </tr>
      );
    }
  });

  return (
    <div class="container-fluid">
      <div class="breadcrumb-wrapper">
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-7 col-sm-7" />
            <div class="col-md-5 col-sm-5 align-right">
              <div class="search-bar">
                <input
                  type="search"
                  alt=""
                  value={props.data.filter.text}
                  class="search-input"
                  placeholder="Search"
                  onChange={props.onSearch}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <div class="table-div">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Name </th>
                    <th>Email</th>
                    <th>No. of Bookings</th>
                    <th>Created On</th>
                    <th>Deactivate User</th>
                  </tr>
                </thead>
                <tbody>{data}</tbody>
              </table>
            </div>
            <div>
              <Pagination
                hideDisabled
                activePage={props.data.filter.pageNo}
                itemsCountPerPage={10}
                totalItemsCount={props.data.total}
                pageRangeDisplayed={5}
                onChange={props.onChange}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = customers;



