import React from "react";
import Edit from "./edit.js";
import Link from "valuelink";
import BlogService from "AdminServices/blog.service.js";
import UserService from "AdminServices/admin.service.js";
import TextEditor from "react-rte";
let links = {};

export default class EditBlog extends React.Component {
  constructor() {
    super();
    this.state = {
      BlogImage: "",
      Name: "",
      By: "",
      Description: "",
      blogId: "",
      Content: TextEditor.createEmptyValue()
    };
  }
  handleChange(value) {
    this.setState({ Content: value });
  }
  componentWillMount() {
    const blogId = this.props.match.params.id;
    BlogService.getBlog(blogId).then(response => {
      let blogObj = response.data[0];
      this.setState({
        Name: blogObj.Name,
        By: blogObj.By,
        Description: blogObj.Description,
        Content: TextEditor.createValueFromString(blogObj.Description, "html"),
        BlogImage: blogObj.BlogImage,
        blogId: blogObj._id,
        UrlDescription: blogObj.UrlDescription
      });
    });
  }

  UploadImage(e) {
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif") &&
      !files[0].type.includes("svg")
    ) {
      formData.append("image", files[0]);

      UserService.UploadImage(formData)
        .then(response => {
          document.getElementById("blogImage").src = response.data;
          this.setState({
            BlogImage: response.data
          });
        })
        .catch(err => {
        });
    } else {
      document.getElementById("blogImage").value = "";
    }
  }

  updateBlog() {
    // this.state.Description = $("#blogdesc").html();
    var newContent = this.state.Content.toString("html");

    this.state.Description = newContent;
    this.state.UrlDescription = this.state.Name.trim().replace(/ /g, "-");
    BlogService.updateBlog(this.state).then(response => {
      if (response.data == "success") {
        this.props.history.push("/admin/blogs");
      }
    });
  }

  render() {
    links = {
      blogNameLink: Link.state(this, "Name").check(
        x => x,
        "Blog Name is required"
      ),
      bylink: Link.state(this, "By").check(x => x, "By is required"),
      descriptionlink: Link.state(this, "Description").check(
        x => x,
        "Description is required"
      )
    };
    const obj = {
      UploadImage: this.UploadImage.bind(this),
      State: this.state,
      updateBlog: this.updateBlog.bind(this),
      links,
      editorChange: this.handleChange.bind(this)
    };
    return <Edit {...obj} />;
  }
}



