import React from "react";
import Add from "./add.js";
import MessageBox from "../common/messagebox.js";
import Helper from "AdminHelpers/utility.helper.js";

let faqObj = "";
const listObj = props => {
  const alertModel = {
    isShow: props.model.ShowAlertModal,
    Message: props.model.Message,
    onClose: props.CloseModal
  };
  const anchorStyle = {
    color: "inherit"
  };
  if (Object.keys(props.model.faqList).length) {
    faqObj = props.model.faqList.map((item, i) => {
      let dateObj = Helper.ConvertDateToString(item.CreateDate);
      return (
        <tr key={i}>
          <td>
            <a
              href={"/admin/editfaq/" + item._id}
              style={{ cursor: "pointer" }}
              //onClick={() => props.EditFaq(item._id)}
            >
              {item.Title}
            </a>
          </td>
          <td>
            <span class="icons">
              <a
                href={"/admin/editfaq/" + item._id}
                // onClick={() => props.EditFaq(item._id)}
              >
                <span class="icon-edit" />
              </a>
            </span>
            <span class="icons">
              <a href="#">
                <span
                  class="icon-trash-can"
                  onClick={() => props.deleteFaq(item._id)}
                />
              </a>
            </span>
          </td>
          <td>{dateObj}</td>

          <td>
            <span class="icons">
              {item.Ispublish ? (
                <a href="javascript:void(0)" style={anchorStyle}>
                  <i class="fa fa-cloud-upload" aria-hidden="true" />
                </a>
              ) : (
                <a
                  href="javascript:void(0)"
                  onClick={() => props.publish(item, true)}
                >
                  <i class="fa fa-cloud-upload" aria-hidden="true" />
                </a>
              )}
            </span>
            <span class="icons">
              {item.Ispublish ? (
                <a
                  href="javascript:void(0)"
                  onClick={() => props.publish(item, false)}
                >
                  <i class="fa fa-cloud-download" aria-hidden="true" />
                </a>
              ) : (
                <a href="javascript:void(0)" style={anchorStyle}>
                  <i class="fa fa-cloud-download active" aria-hidden="true" />
                </a>
              )}
            </span>
          </td>
        </tr>
      );
    });
  }
  return (
    <div class="container-fluid">
      <MessageBox {...alertModel} />
      {!props.model.showAddFaq && (
        <div class="row">
          <div class="col-lg-12">
            <div class="table-div margin-top-8x">
              <div class="table-responsive advertisement-table">
                <table class="table">
                  <thead>
                    <tr>
                      <th>Title</th>
                      <th>Actions</th>
                      <th>Date</th>
                      <th>Publish / Un-Publish</th>
                    </tr>
                  </thead>
                  <tbody>{faqObj}</tbody>
                </table>
              </div>
              <div class="align-center margin-bottom-6x">
                <a
                  class="btn btn-success faq-btn align-center"
                  href="/admin/addfaq"
                >
                  Add Questions and Answers
                </a>
              </div>
            </div>
          </div>
        </div>
      )}
      {props.model.showAddFaq && <Add {...props} />}
    </div>
  );
};

module.exports = listObj;



