import { http } from "AdminHelpers/axios.helper.js";
export default class reportService {
  static UsersReport(type, duration) {
    const url =
      "/api/admin/reports/users?type=" + type + "&duration=" + duration;
    return http.get(url);
  }
  static RevenueReport(duration) {
    const url = "/api/admin/reports/revenue?duration=" + duration;
    return http.get(url);
  }
  static RatingReport(duration) {
    const url = "/api/admin/reports/rating?duration=" + duration;
    return http.get(url);
  }
}



