import React from "react";
import VInput from "Components/common/VInput";

const list = props => {
  const listData = props.data.list.map((val, key) => {
    return (
      <div class="row newsletter-list margin-top-8x margin-bottom-4x" key={key}>
        <div class="col-md-2">
          <img
            src={val.Image}
            onError={e => {
              e.target.src = "/Content/images/default-user.jpg";
            }}
          />
        </div>
        <div class="col-md-8">
          <p>{val.name}</p>
          <p>{val.Description}</p>
        </div>
        <div class="col-md-2 align-right">
          <button
            class="btn btn-danger"
            onClick={() => {
              props.delete(val._id);
            }}
          >
            Delete
          </button>
        </div>
      </div>
    );
  });
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <h3 class="margin-bottom-6x">Manage Testimonials</h3>
          <div class="margin-bottom-4x align-center">
            <div class="imageupload panel panel-default">
              <div class="panel-heading clearfix">
                <h3 class="panel-title pull-left">Upload Image</h3>
              </div>
              <div class="file-tab panel-body">
                <label class="btn btn-success btn-file">
                  <span>Browse</span>
                  <input
                    type="file"
                    name="image-file"
                    id="image-file"
                    onChange={props.onUploadimage}
                    accept="image/*"
                  />
                </label>
                <button
                  type="button"
                  class="btn btn-danger"
                  id="removeImage"
                  name="removeImage"
                >
                  Remove
                </button>
              </div>
            </div>
            <br />
            <div class="form-wrap news-form margin-top-6x">
              <div class="form-group">
                <label>Enter Name</label>
                <VInput
                  type="text"
                  className="form-control"
                  name="Name"
                  placeholder="Name*"
                  valueLink={props.links.nameLink}
                />
              </div>
              <div class="form-group">
                <label>Enter description</label>
                <VInput
                  type="text"
                  className="form-control"
                  name="Description"
                  placeholder="Description*"
                  valueLink={props.links.descLink}
                />
              </div>
              <div class="form-group align-center setting-page">
                <button class="btn btn-success" onClick={props.save}>
                  Save
                </button>
                <button class="btn btn-default" onClick={props.cancel}>
                  Cancel
                </button>
              </div>
            </div>
          </div>
          {listData}
        </div>
      </div>
    </div>
  );
};
module.exports = list;



