import React from "react";
import Review from "./review.js";
import ReviewService from "AdminServices/review.service.js";

export default class WareReview extends React.Component {
  constructor() {
    super();
    this.state = {
      activePage: 1,
      totalCount: 0,
      WareName: "",
      wareReviewList: {}
    };
    this.deleteReview = this.deleteReview.bind(this);
  }

  reviewlist(skipCount) {
    const warehouseId = this.props.match.params.id;
    ReviewService.warehousereview(skipCount, warehouseId).then(response => {
      this.setState({ wareReviewList: response.data });
    });
  }

  componentWillMount() {
    const wareId = this.props.match.params.id;
    this.setState({ WareName: this.props.match.params.name });
    this.reviewlist(0);
    ReviewService.all(wareId).then(response => {
      this.setState({ totalCount: response.data.length });
    });
  }

  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber });
    let skipCount = (pageNumber - skip) * 10;
    this.reviewlist(skipCount);
  }
  deleteReview(reviewId) {
    let index = -1;
    let array = this.state.wareReviewList;

    ReviewService.deleteReview(reviewId).then(response => {
      if (response.data == "success") {
        array.filter(item => {
          if (item._id == reviewId) {
            index = array.indexOf(item);
            return false;
          }
        });
        array.splice(index, 1);
        this.setState({ wareReviewList: array });
      }
    });
  }
  render() {
    const reviewObj = {
      model: this.state,
      handlePageChange: this.handlePageChange.bind(this),
      deleteReview: this.deleteReview
    };

    return <Review {...reviewObj} />;
  }
}



