import React from "react";

const report = props => {
  return (
    <div class="container-fluid">
      <div class="header">
        <h2>Reports</h2>
      </div>
      <div class="mainContent">
        <div class="innerPadding padding-top-6x padding-bottom-6x">
          <div class="reportWarpping">
            <div>
              <div class="row" style={{ paddingBottom: 20 }}>
                <div class="col-xs-6 col-sm-3 col-md-3">
                  <select
                    class="form-control"
                    id="Duration"
                    name="Duration"
                    value={props.model.Duration}
                    onChange={props.onchange}
                  >
                    <option disabled>Select Value</option>
                    <option value="year">Year</option>
                    <option value="month">Past Month</option>
                    <option value="week">Past Week</option>
                  </select>
                </div>
                <div class="col-xs-6 col-sm-3 col-md-3">
                  <select
                    class="form-control"
                    id="Type"
                    name="Type"
                    value={props.model.Type}
                    onChange={props.onchange}
                  >
                    <option disabled>Select Value</option>
                    <option value="owner">Warehouse Users</option>
                    <option value="buyer">Warehouse Customers</option>
                    <option value="revenue">Revenue</option>
                    <option value="rating">Negative Rating</option>
                  </select>
                </div>

                <div class="col-md-6">
                  <div class="setting-report">
                    {/* <a href="javascript:void(0);">
                    <i class="fa fa-cog" aria-hidden="true" />
                  </a> */}
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="tab-content tabs">
                  <div
                    role="tabpanel"
                    class="tab-pane fade in active"
                    id="Section1"
                  >
                    <div class="reportWarp">
                      <div class="form-group">
                        <div class="__lineChart margin-bottom-4x">
                          <canvas id="chart-legend-top" />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default report;



