import React from "react";

const Rating = ({ input, onclick }) => {
  const val = Math.round(input);
  const starts = [1, 2, 3, 4, 5].map(x =>
    <span
      class="star"
      key={x}
      style={{ WebkitTextStroke: "1px #777", color: "#fde16d" }}
    >
      <i
        class={x <= val ? "fa fa-star" : "fa fa-star-o"}
        onClick={() => onclick(x)}
      />
    </span>
  );
  return (
    <div>
      {starts}
    </div>
  );
};

export default Rating;



