import React from "react";
import Index from "./index.js";
import UserService from "AdminServices/admin.service.js";

export default class adminDashboard extends React.Component {
  constructor() {
    super();
    this.state = { obj: {} };
  }

  componentWillMount() {
    UserService.dashboardcount().then(response => {
      this.setState({ obj: response.data.Obj });
    });
  }

  render() {
    return <Index {...this.state.obj} />;
  }
}



