import React from "react";
import Pagination from "react-js-pagination";
// import Helper from "Helpers/utility.helper.js";
import Helper from "../../helpers/utility.helper.js";
const wareDetail = props => {
  let listObj = "";
  let service = "";
  let secrity = "";
  let storagetype = "";
  let from = "";
  let to = "";
  let warehouseType = "";
  let timeAvailability = "";
  if (Object.keys(props.model.wareDetail).length) {
    from = Helper.timeConvert(props.model.wareDetail[0].Starthrs);
    to = Helper.timeConvert(props.model.wareDetail[0].Endhrs);
    warehouseType = props.model.wareDetail[0].StorageType;
    if (warehouseType == "Pallet") {
      timeAvailability = <p>{Helper.ConvertDateToString(props.model.wareDetail[0].Pallet.AvailabilityFrom) + " - " + Helper.SetEndDate(props.model.wareDetail[0].Pallet.AvailabilityTo)}</p>;
    } else if (warehouseType == "Bulk") {
      timeAvailability = <p>{Helper.ConvertDateToString(props.model.wareDetail[0].Bulk.AvailabilityFrom) + " - " + Helper.SetEndDate(props.model.wareDetail[0].Bulk.AvailabilityTo)
      }</p>;
    } else {
      timeAvailability = <p>Pallet = {Helper.ConvertDateToString(props.model.wareDetail[0].Pallet.AvailabilityFrom) + " - " + Helper.SetEndDate(props.model.wareDetail[0].Pallet.AvailabilityTo)}
        <br />Bulk = {Helper.ConvertDateToString(props.model.wareDetail[0].Bulk.AvailabilityFrom) + " - " + Helper.SetEndDate(props.model.wareDetail[0].Bulk.AvailabilityTo)}</p>
    }
  }

  if (Object.keys(props.model.wareDetail).length) {
    if (props.model.wareDetail[0].Services) {
      Object.keys(props.model.wareDetail[0].Services).map(function (key, index) {
        if (props.model.wareDetail[0].Services[key]) {
          service += key + " ,";
        }
      });
    }
    if (service.indexOf("others") > -1) {
      service = service.replace(
        "others",
        props.model.wareDetail[0].OtherService
      );
    }
    if (props.model.wareDetail[0].Security) {
      Object.keys(props.model.wareDetail[0].Security).map(function (key, index) {
        if (props.model.wareDetail[0].Security[key]) {
          secrity += key + " ,";
        }
      });
    }
    if (secrity.indexOf("others") > -1) {
      secrity = secrity.replace(
        "others",
        props.model.wareDetail[0].OtherSpecify
      );
    }
    if (props.model.wareDetail[0].WarehouseType) {
      Object.keys(props.model.wareDetail[0].WarehouseType).map((key, index) => {
        if (props.model.wareDetail[0].WarehouseType[key]) {
          storagetype += key + " ,";
        }
      });
    }
  }

  if (Object.keys(props.model.Userlist).length) {
    listObj = props.model.Userlist.map(item => {
      let fromdate = "";
      let todate = "";
      let TotalSpace = "";

      if (item.StorageType == "Pallet" && item.Pallets.length) {
        fromdate = item.Pallets[0].FromDate;
        todate = item.Pallets[0].ToDate;
        TotalSpace = item.Pallets[0].TotalSpace;
      }
      if (item.StorageType == "Bulk" && item.Bulks.length) {
        fromdate = item.Bulks[0].FromDate;
        todate = item.Bulks[0].ToDate;
        TotalSpace = item.Bulks[0].TotalSpace;
      }

      return (
        <tr>
          <td>{item.name}</td>
          <td>{item.email}</td>
          <td>
            {fromdate} - {todate}
          </td>
          <td>{item.StorageType}</td>
          <td>{TotalSpace}</td>
          <td>&pound; {item.cost}</td>
        </tr>
      );
    });
  }
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center warehouse-dtl">
        <div class="col-lg-12">
          <h3>
            {Object.keys(props.model.wareDetail).length > 0 &&
              props.model.wareDetail[0].Name}
          </h3>
          <div class="form-wrap margin-top-6x">
            <div class="form-group align-left">
              <label>Warehouse Address</label>
              {Object.keys(props.model.wareDetail).length > 0 && (
                <p>{props.model.wareDetail[0].Address}</p>
              )}
            </div>
            <div class="form-group align-left">
              <label>Warehouse Capacity</label>
              <p>
                {Object.keys(props.model.wareDetail).length > 0
                  ? props.model.wareDetail[0].CapacityType == "Feet"
                    ? Helper.SqMeterToSqFeet(
                      props.model.wareDetail[0].Capacity
                    ) +
                    " Sq " +
                    props.model.wareDetail[0].CapacityType
                    : props.model.wareDetail[0].Capacity +
                    " Sq " +
                    props.model.wareDetail[0].CapacityType
                  : ""}
              </p>
            </div>

            <h4 class="margin-top-8x margin-bottom-3x align-left">
              Opening hours
            </h4>
            <div class="form-group align-left">
              <label>From To</label>
              <p>
                {from} to {to}
              </p>
            </div>
            <div class="form-group align-left">
              <label>Storage Type</label>
              <p>{warehouseType}</p>
            </div>
            <div class="form-group align-left">
              <label>Available Space</label>
              <p>{Object.keys(props.model.wareDetail).length > 0
                ? props.model.wareDetail[0].CapacityType == "Feet"
                  ? Helper.SqMeterToSqFeet(
                    props.model.wareDetail[0].AvailableSpace
                  ) +
                  " Sq " +
                  props.model.wareDetail[0].CapacityType
                  : props.model.wareDetail[0].AvailableSpace +
                  " Sq " +
                  props.model.wareDetail[0].CapacityType
                : ""}</p>
            </div>
            <div class="form-group align-left">
              <label>Warehouse Availablity</label>
              <p>{timeAvailability}</p>
            </div>
            <div class="form-group align-left">
              <label>Security</label>
              <p>{secrity}</p>
            </div>
            <div class="form-group align-left">
              <label>Services</label>
              <p>{service}</p>
            </div>
            <div class="form-group align-left">
              <label>Warehouse Type</label>
              <p>{storagetype}</p>
            </div>
          </div>
          <div class="table-div">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Booking Name </th>
                    <th>Email</th>
                    <th>From - To</th>
                    <th>Booking Type</th>
                    <th>Space occupied</th>
                    <th>Payment</th>
                  </tr>
                </thead>
                <tbody>{listObj}</tbody>
              </table>
            </div>
            <div>
              <Pagination
                hideDisabled
                activePage={props.model.activePage}
                itemsCountPerPage={10}
                totalItemsCount={props.model.totalCount}
                pageRangeDisplayed={5}
                onChange={props.handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = wareDetail;



