import React from "react";
const message = props => {
  return (
    <div
      class={props.isShow ? "modal fade in" : "modal fade"}
      id="myModal-ALert"
      tabIndex="-1"
      role="dialog"
      aria-labelledby="myModalLabel-Alert"
      style={{ display: props.isShow ? "block" : "none" }}
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content forgot">
          <div class="closewrap">
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              onClick={props.onClose}
            >
              &times;
            </button>
          </div>
          <div class="modal-body forgot-password">
            <div class="headpassword">
              <h2 />
              <p>
                {props.Message}
              </p>
            </div>

            <div class="formWarp password">
              <div class="form-group">
                <input
                  type="button"
                  class="popup-btn"
                  value="Ok"
                  onClick={props.onClose}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default message;



