import React from "react";
import VInput from "Components/common/VInput";
import MessageBox from "../common/messagebox.js";

const adminLogin = props => {
  const alertModel = {
    isShow: props.Model.ShowAlertModal,
    Message: props.Model.Message,
    onClose: props.CloseModal
  };
  return (
    <div class="outer">
      <MessageBox {...alertModel} />
      <div class="middle">
        <div class="inner">
          {props.Model.showLogin &&
            <div class="login-wrapper admin-wrapper">
              <div class="text-center">
                <img src="/Content/images/logist-compare.svg" alt="logo" />
              </div>

              <div class="login-widget admin-login">
                <div class="panel">
                  <div class="panel-heading clearfix">
                    <div class="pull-left texttransform">
                      <label> Sign in</label>
                    </div>
                  </div>

                  <div class="panel-body">
                    <form class="form-login" action="" method="">
                      <div class="form-group">
                        <VInput
                          type="text"
                          name="Email"
                          class="form-control"
                          id="email"
                          placeholder="Email"
                          autoComplete="off"
                          onKeyPress={props.onKeyPress}
                          valueLink={props.links.emialLink}
                        />
                        {/*<label for="email">Email</label>*/}
                      </div>
                      <div class="form-group margin-top-6x margin-bottom-6x">
                        <VInput
                          type="password"
                          name="Password"
                          class="form-control"
                          id="pass"
                          placeholder="Password"
                          autoComplete="off"
                          onKeyPress={props.onKeyPress}
                          valueLink={props.links.passwordLink}
                        />
                        {/*<label for="pass">Password</label>*/}
                      </div>
                      <div class="form-group margin-top-6x">
                        <input
                          type="button"
                          class="btn btn-success btn-sm texttransform"
                          value="Sign in"
                          onClick={props.onClick}
                        />
                      </div>
                      <div class="forget-txt  align-center">
                        <a
                          href=""
                          onClick={() => props.showForgot("showForgot")}
                        >
                          Forgot Password?
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>}
          {!props.Model.showLogin &&
            <div class="login-wrapper admin-wrapper">
              <div class="text-center">
                <img src="/Content/images/logist-compare.svg" alt="logo" />
              </div>

              <div class="login-widget admin-login animation-delay1">
                <div class="panel">
                  <div class="panel-heading clearfix">
                    <div class="pull-left texttransform">
                      <label> Forgot Password</label>
                    </div>
                    <p>
                      Please enter your email id. A password will be shared on
                      the same email id
                    </p>
                  </div>
                  <div class="panel-body">
                    <form class="form-login" action="" method="">
                      <div class="form-group margin-top-6x margin-bottom-6x">
                        <input
                          type="text"
                          name="Forgotemail"
                          class="form-control"
                          id="email"
                          placeholder="Email"
                          autoComplete="off"
                          value={props.Model.Forgotemail}
                          onChange={props.handleChange}
                        />
                      </div>

                      <div class="form-group margin-top-6x">
                        <input
                          type="button"
                          class="btn btn-success btn-sm texttransform"
                          value="Submit"
                          onClick={props.ForgotSubmit}
                        />
                      </div>
                      <div class="forget-txt  align-center">
                        <a
                          href=""
                          onClick={() => props.showForgot("showLogin")}
                        >
                          Back to Login
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>}
        </div>
      </div>
    </div>
  );
};
module.exports = adminLogin;



