import React from "react";
import Pagination from "react-js-pagination";
import Service from "../../../adminsrc/helpers/utility.helper.js";

const contactusList = props => {
  const ALertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const errorModal = {
    ModalCss: props.errorModal ? "in" : "",
    ModalStyle: { display: props.errorModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const SuccessModal = {
    ModalCss: props.SuccessModal ? "in" : "",
    ModalStyle: { display: props.SuccessModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const ShowMessageModal = {
    ModalCss: props.ShowMessageModal ? "in" : "",
    ModalStyle: { display: props.ShowMessageModal ? "block" : "none" },
    onClose: props.onClose
  };
  let listObj = "";
  if (Object.keys(props.model.messageList).length) {
    listObj = props.model.messageList.map((item, key) => {
      return (
        <tr key={key}>
          <td>{item.Name}</td>
          <td> {item.Email}</td>
          <td> {item.Subject}</td>
          <td> {item.Message.substring(0, 30)}...</td>
          <td> {Service.ConvertDateToString(item.CreatedDate)}</td>
          <td>
            <span class="icons contact-icon">
              <a href="#" onClick={() => props.viewMessage(item)}>
                <span>
                  <i class="fa fa-eye" aria-hidden="true" />
                </span>
              </a>
            </span>
            <span class="icons contact-icon">
              <a href="#" onClick={() => props.RemoveMessage(item)}>
                <span class="icon-trash-can" />
              </a>
            </span>
          </td>
        </tr>
      );
    });
  }
  return (
    <div class="container-fluid">
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <div class="table-div">
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Email Address</th>
                    <th>Subject</th>
                    <th>Message</th>
                    <th>Enquiry Date</th>
                    <th />
                  </tr>
                </thead>
                <tbody>{listObj}</tbody>
              </table>
            </div>

            <div>
              <Pagination
                hideDisabled
                activePage={props.model.activePage}
                itemsCountPerPage={10}
                totalItemsCount={props.model.totalCount}
                pageRangeDisplayed={5}
                onChange={props.handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
      <div
        id="myModal"
        class={"modal fade" + ALertModal.ModalCss + "nc-form-decline"}
        role="dialog"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-body align-center">
              <h3 class="margin-bottom-4x">
                Are you sure you want to Delete the Message?
              </h3>

              <div class="form-group margin-top-6x">
                <div class="input">
                  <input
                    type="submit"
                    alt=""
                    onClick={props.DeleteMessage}
                    value="Yes"
                    class="popup-btn"
                  />
                  <input
                    type="submit"
                    alt=""
                    value="Cancel"
                    onClick={errorModal.onClose}
                    class="popup-btn"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + errorModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={errorModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={errorModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{errorModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="popup-btn"
                    value="Ok"
                    onClick={errorModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>{SuccessModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <button
                    href="javascript:void(0)"
                    class="popup-btn"
                    onClick={SuccessModal.onClose}
                  >
                    Ok
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        id="myModal"
        class={"modal fade" + ShowMessageModal.ModalCss + "nc-form-decline"}
        role="dialog"
        style={ShowMessageModal.ModalStyle}
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-body align-center">
              <h3 class="margin-bottom-4x">View Detail</h3>

              <div class="form-group margin-top-6x">
                {props.model.MessageDetail}
                <div class="formWarp password">
                  <div class="form-group">
                    <button
                      href="javascript:void(0)"
                      class="popup-btn"
                      onClick={SuccessModal.onClose}
                    >
                      Ok
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = contactusList;



