import { http } from "AdminHelpers/axios.helper.js";

export default class ManagePageService {
  static GetContent(page) {
    let url = "/api/content/getcontent?page=" + page;
    return http.get(url);
  }
  static UpdateContent(page, htmlContent) {
    let url = "/api/content/updatecontent?page=" + page;
    return http.post(url, htmlContent);
  }
  static UploadImage(data) {
    const url = "/api/content/uploadbackgroundimage";
    return http.post(url, data);
  }
  static RemoveImage(data) {
    const url = "/api/content/removeimage";
    return http.post(url, data);
  }
}



