import React from "react";
import MessageBox from "../common/messagebox.js";
import List from "./list.js";

const videoObj = props => {
  const alertModel = {
    isShow: props.model.ShowAlertModal,
    Message: props.model.Message,
    onClose: props.CloseModal
  };
  return (
    <div class="container-fluid">
      <MessageBox {...alertModel} />
      <div class="row margin-top-8x margin-bottom-8x align-center">
        <div class="col-lg-12">
          <h3 class="margin-bottom-6x">Manage Videos</h3>
          <div class="margin-bottom-4x align-center">
            <div class="full-width">
              <div class="form-wrap news-form margin-top-6x">
                <div class="form-group">
                  <label>Enter URL</label>
                  <input
                    type="text"
                    name="Url"
                    value={props.model.model.Url}
                    onChange={props.change}
                    class="form-control"
                  />
                </div>
                <div class="form-group">
                  <label>Enter description</label>
                  <textarea
                    rows=""
                    cols=""
                    name="Description"
                    value={props.model.model.Description}
                    class="form-control"
                    onChange={props.change}
                  />
                </div>
                <div class="form-group align-center setting-page">
                  <button class="btn btn-success" onClick={props.save}>
                    {props.model.buttontext}
                  </button>
                  <button class="btn btn-default" onClick={props.reset}>
                    Reset
                  </button>
                </div>
              </div>
            </div>
          </div>

          <List {...props} />
        </div>
      </div>
    </div>
  );
};

module.exports = videoObj;



