import { http } from "AdminHelpers/axios.helper.js";

export default class ReviewService {
  static warehousereview(pageNo, wareId) {
    const url =
      "/api/warehouse/getWarehouseReview?pageNo=" +
      pageNo +
      "&wareId=" +
      wareId;
    return http.get(url);
  }
  static deleteReview(reviewId) {
    const url = "/api/warehouse/deleteReviewById/" + reviewId;
    return http.get(url);
  }
  static all(reviewId) {
    const url = "/api/warehouse/allReviewList/" + reviewId;
    return http.get(url);
  }
}



