import React from "react";
import Solution from "./solutions.js";
import TextEditor from "react-rte";
import ContentService from "AdminServices/content.service.js";
let CurrentPage = "";
export default class MySolutions extends React.Component {
  constructor(props) {
    super();
    this.state = {
      Content: TextEditor.createEmptyValue(),
      BackGroundImage: "banner_logist.jpg",
      ContentHtml: "",
      ShowAlertModal: false,
      Message: "Content changed successfully"
    };
    if (props.location.pathname.indexOf("/admin/manage_whyus") > -1) {
      CurrentPage = "Why Us";
    }
    if (props.location.pathname.indexOf("/admin/manage_aboutus") > -1) {
      CurrentPage = "About Us";
    }
    if (props.location.pathname.indexOf("/admin/manage_solution") > -1) {
      CurrentPage = "Solution";
    }
    if (props.location.pathname.indexOf("/admin/manage_policy") > -1) {
      CurrentPage = "Privacy Policy";
    }
    if (props.location.pathname.indexOf("/admin/manage_terms") > -1) {
      CurrentPage = "Terms and Conditions";
    }
  }
  //

  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }
  componentWillMount() {
    this.ResetContent();
  }
  UpdateContent() {
    var newContent = this.state.Content.toString("html");
    var Data = {
      Content: newContent,
      Image: this.state.BackGroundImage
    };
    ContentService.UpdateContent(CurrentPage, Data)
      .then(response => {
        this.setState({ ShowAlertModal: true });
      })
      .catch(error => {
      });
  }
  ResetContent() {
    ContentService.GetContent(CurrentPage).then(response => {
      if (response.data.Status) {
        this.setState({
          Content: TextEditor.createValueFromString(
            response.data.Content,
            "html"
          )
        });
      }
    });
  }
  handleChange(value) {
    this.setState({ Content: value });
  }
  uploadImage(e) {
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif") &&
      !files[0].type.includes("svg")
    ) {
      formData.append("image", files[0]);
      ContentService.UploadImage(formData)
        .then(response => {
          this.setState({
            BackGroundImage: response.data
          });
        })
        .catch(err => {
        });
    }
  }
  RemoveImage() {
    ContentService.RemoveImage(this.state.BackGroundImage)
      .then(response => {
        this.setState({
          BackGroundImage: response.data.BackgroundImage
        });
      })
      .catch(err => {
      });
  }
  render() {
    const solutionObj = {
      editorChange: this.handleChange.bind(this),
      Reset: this.ResetContent.bind(this),
      Update: this.UpdateContent.bind(this),
      uploadImage: this.uploadImage.bind(this),
      removeImage: this.RemoveImage.bind(this),
      CloseModal: this.CloseModal.bind(this),
      Title: CurrentPage,
      State: this.state
    };
    return <Solution {...solutionObj} />;
  }
}



