import React, { Component } from "react";
import Reports from "./reports.js";
import Service from "AdminServices/report.service.js";
export default class reportContainer extends Component {
  constructor() {
    super();
    this.state = {
      Duration: "year",
      Type: "owner",
      ReportList: [],
      isLoading: true
    };
    this.createConfig = this.createConfig.bind(this);
    this.onchange = this.onchange.bind(this);
  }
  createConfig(legendPosition, colorName, color, labels, data) {
    return {
      type: "line",
      data: {
        labels: labels,
        datasets: [
          {
            label: this.state.Type,
            data: data,
            backgroundColor: color(window.chartColors[colorName])
              .alpha(0.5)
              .rgbString(),
            borderColor: window.chartColors[colorName],
            borderWidth: 3
          }
        ]
      },
      options: {
        responsive: true,
        legend: {
          position: legendPosition
        },
        scales: {
          xAxes: [
            {
              display: true,
              scaleLabel: {
                display: true,
                labelString: this.state.Duration
              }
            }
          ],
          yAxes: [
            {
              display: true,
              scaleLabel: {
                display: true,
                labelString: "Value"
              }
            }
          ]
        },
        title: {
          display: false,
          text: "Legend Position: " + legendPosition
        },
        hover: {
          mode: "label"
        },
        tooltips: {
          enabled: true,
          callbacks: {
            label: function(tooltipItem, data) {
              var text =
                data.datasets[tooltipItem.datasetIndex].label == "revenue"
                  ? " " + tooltipItem.yLabel
                  : tooltipItem.yLabel;
              return (
                data.datasets[tooltipItem.datasetIndex].label + ": " + text
              );
            }
          }
        }
      }
    };
  }
  showChart(labels, data) {
    const self = this;
    const color = Chart.helpers.color;
    [
      {
        id: "chart-legend-top",
        legendPosition: "top",
        color: "blue"
      }
    ].forEach(function(details) {
      var ctx = document.getElementById(details.id).getContext("2d");
      var config = self.createConfig(
        details.legendPosition,
        details.color,
        color,
        labels,
        data
      );
      new Chart(ctx, config);
    });
  }
  onchange(e) {
    e.preventDefault();
    const { name, value } = e.target;
    if (name == "Duration") {
      this.GenerateReport(this.state.Type, value);
    } else if (name == "Type") {
      this.GenerateReport(value, this.state.Duration);
    }
    this.setState({ [name]: value });
  }
  GenerateReport(type, duration) {
    this.setState({ isLoading: true });
    if (type == "rating") {
      Service.RatingReport(duration)
        .then(response => {
          const data = response.data.map(val => val.Count);
          const labels = response.data.map(val => {
            if (val._id.CreatedDate.day)
              return (
                val._id.CreatedDate.day +
                "/" +
                val._id.CreatedDate.month +
                "/" +
                val._id.CreatedDate.year
              );
            else
              return val._id.CreatedDate.month + "/" + val._id.CreatedDate.year;
          });
          this.showChart(labels, data);
          this.setState({ isLoading: false });
        })
        .catch(error => {
          console.log(error);
        });
    } else if (type == "revenue") {
      Service.RevenueReport(duration)
        .then(response => {
          const data = response.data.map(val => {
            return parseFloat(val.Count).toFixed(2);
          });
          const labels = response.data.map(val => {
            if (val._id.CreatedDate.day)
              return (
                val._id.CreatedDate.day +
                "/" +
                val._id.CreatedDate.month +
                "/" +
                val._id.CreatedDate.year
              );
            else
              return val._id.CreatedDate.month + "/" + val._id.CreatedDate.year;
          });
          this.showChart(labels, data);
          this.setState({ isLoading: false });
        })
        .catch(error => {
          console.log(error);
        });
    } else {
      Service.UsersReport(type, duration)
        .then(response => {
          const data = response.data.map(val => val.Count);
          const labels = response.data.map(val => {
            if (val._id.CreateDate.day)
              return (
                val._id.CreateDate.day +
                "/" +
                val._id.CreateDate.month +
                "/" +
                val._id.CreateDate.year
              );
            else
              return val._id.CreateDate.month + "/" + val._id.CreateDate.year;
          });
          this.showChart(labels, data);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }

  componentDidMount() {
    this.GenerateReport("owner", "year");
  }
  render() {
    const model = {
      model: this.state,
      onchange: this.onchange
    };
    return <Reports {...model} />;
  }
}



