import React from "react";

import { Set } from "Helpers/axios.helper.js";

import { withRouter, Link } from "react-router-dom";

class AdminSideHeader extends React.Component {
  constructor() {
    super();
    this.logout = this.logout.bind(this);
  }
  logout() {
    localStorage.removeItem("userinfo");
    Set(null);
    this.props.history.push("/admin/login");
  }
  render() {
    return (
      <div>
        <nav class="navbar navbar-inverse navbar-fixed-top" role="navigation">
          <div class="navbar-innr">
            <div class="navbar-header">
              <button
                type="button"
                class="navbar-toggle"
                data-toggle="collapse"
                data-target=".navbar-ex1-collapse"
              >
                <span class="sr-only">Toggle navigation</span>
                <span class="icon-bar" />
                <span class="icon-bar" />
                <span class="icon-bar" />
              </button>
              <a class="navbar-brand" href="/admin/dashboard">
                <img src="/Content/images/logist-compare.svg" alt="" />
              </a>
            </div>
            <ul class="nav navbar-right top-nav">
              <li class="dropdown">
                <a href="#" class="dropdown-toggle" data-toggle="dropdown">
                  Hi, Admin <b class="caret" />
                </a>
                <ul class="dropdown-menu">
                  <li>
                    <a href="#" onClick={() => this.logout()}>
                      <span class="icon-logout" /> Log Out
                    </a>
                  </li>
                </ul>
              </li>
            </ul>
            <div class="collapse navbar-collapse navbar-ex1-collapse">
              <ul class="nav navbar-nav side-nav">
                <li class="active">
                  <Link to="/admin/dashboard">Dashboard</Link>
                </li>
                <li>
                  <Link to="/admin/manage_solution">Manage Solutions</Link>
                </li>
                <li>
                  <Link to="/admin/managevideo">Manage Videos</Link>
                </li>
                <li>
                  <Link to="/admin/manage_aboutus">About Us</Link>
                </li>
                <li>
                  <Link to="/admin/manage_whyus">Why Us</Link>
                </li>

                <li>
                  <Link to="/admin/faq">FAQ</Link>
                </li>
                <li>
                  <Link to="/admin/blogs">Blogs</Link>
                </li>
                <li>
                  <Link to="/admin/testimonials">Testimonials</Link>
                </li>
                <li>
                  <Link to="/admin/inbox">Contact Us Queries</Link>
                </li>
                <li>
                  <Link to="/admin/manage_policy">Privacy Policy</Link>
                </li>
                <li>
                  <Link to="/admin/newsletters">Newsletter</Link>
                </li>
                <li>
                  <Link to="/admin/manage_terms">Terms and Conditions</Link>
                </li>
                <li>
                  <Link to="/admin/reports">Reports</Link>
                </li>
                <li>
                  <Link to="/admin/subscriptions">Subscription Requests</Link>
                </li>
                <li>
                  <Link to="/admin/requests">Enquiry Requests</Link>
                </li>
                <li>
                  <Link to="/admin/setting">Settings</Link>
                </li>
              </ul>
            </div>
          </div>
          <div class="breadcrumb-wrapper">
            <div class="container-fluid">
              <div class="row">
                <div class="col-md-7 col-sm-7">
                  <ol class="breadcrumb">
                    <li class="active">Dashboard</li>
                  </ol>
                </div>
                <div class="col-md-5 col-sm-5 align-right" />
              </div>
            </div>
          </div>
        </nav>
        <div id="wrapper">{this.props.children}</div>
      </div>
    );
  }
}

module.exports = withRouter(AdminSideHeader);



