import { http } from "AdminHelpers/axios.helper.js";

export default class UserService {
  static login(user) {
    const url = "/api/user/adminlogin";
    return http.post(url, user);
  }
  static forgotPassword(emailId) {
    const url = "/api/user/adminforgotpassword";
    return http.post(url, emailId);
  }
  static dashboardcount() {
    const url = "/api/user/count";
    return http.get(url);
  }

  static getUserInformation(userId) {
    const url = "/api/user/getUserInformationById/" + userId;
    return http.get(url);
  }
  static changePassword(data) {
    const url = "/api/user/adminChangePassword";
    return http.post(url, data);
  }
  static manageVideo(data) {
    const url = "/api/user/saveVideo";
    return http.post(url, data);
  }
  static all() {
    var url = "/api/user/getManageVideo";
    return http.get(url);
  }
  static edit(id) {
    const url = "/api/user/editVideo/" + id;
    return http.get(url);
  }
  static deleteVideo(id) {
    const url = "/api/user/deleteVideo/" + id;
    return http.get(url);
  }
  static saveFaq(data) {
    const url = "/api/user/addFaq";
    return http.post(url, data);
  }
  static getAllFaq(pageNo) {
    const url = "/api/user/getFaqList/" + pageNo;
    return http.get(url);
  }
  static Editfaq(id) {
    const url = "/api/user/editFaq/" + id;
    return http.get(url);
  }
  static deleteFaq(id) {
    const url = "/api/user/deletefaqById/" + id;
    return http.get(url);
  }
  static isFaqPublish(id, isPublish) {
    const url = "/api/user/faqpublish?Id=" + id + "&ispublish=" + isPublish;
    return http.get(url);
  }
  static UploadImage(data) {
    const url = "/api/user/uploadBlogimage";
    return http.post(url, data);
  }
  static getUserRoleList(userId) {
    const url = "/api/user/userRoleList/" + userId;
    return http.get(url);
  }
}



