import React, { Component } from "react";
import Customers from "./customers.js";
import Service from "AdminServices/customers.service.js";
export default class customersContainer extends Component {
  constructor() {
    super();
    this.state = {
      result: [],
      total: 0,
      filter: {
        text: "",
        pageNo: 1
      }
    };
    this.getData = this.getData.bind(this);
    this.getTotal = this.getTotal.bind(this);
    this.onPageChange = this.onPageChange.bind(this);
    this.onSearch = this.onSearch.bind(this);
    this.changeStatus = this.changeStatus.bind(this);
  }
  componentWillMount() {
    this.getData(this.state.filter);
    this.getTotal(this.state.filter);
  }
  getData(data) {
    Service.All(data)
      .then(response => {
        this.setState({ result: response.data });
      })
      .catch(err => {
        console.log(err);
      });
  }
  getTotal(data) {
    Service.Count(data)
      .then(response => {
        this.setState({ total: response.data });
      })
      .catch(err => {
        console.log(err);
      });
  }
  changeStatus(userId, status) {
    Service.UpdateStatus({ id: userId, status: status })
      .then(response => {
        const result = this.state.result;
        const user = result.find(x => x._id.userId == userId);
        const index = result.indexOf(user);
        user._id.Status = status;
        result[index] = user;

        this.setState({ result });
      })
      .catch(err => {
        console.log(err);
      });
  }
  onPageChange(pageNo) {
    const filter = { ...this.state.filter, pageNo: pageNo };
    this.setState({ filter });
    this.getData(filter);
  }

  onSearch(e) {
    e.preventDefault();
    const value = e.target.value;
    const filter = { pageNo: 1, text: value };
    this.getData(filter);
    this.getTotal(filter);
    this.setState({ filter });
  }
  render() {
    const model = {
      data: this.state,
      onChange: this.onPageChange,
      onSearch: this.onSearch,
      changeStatus: this.changeStatus
    };

    return <Customers {...model} />;
  }
}



