import { http } from "AdminHelpers/axios.helper.js";

export default class ContactService {
  static All(data) {
    const url = "/api/contact/getmessage?pageno=" + data.pageNo;
    return http.get(url);
  }
  static MessageCount() {
    const url = "/api/contact/count";
    return http.get(url);
  }
  static RemoveMessage(id) {
    const url = "/api/contact/delete?id=" + id;
    return http.get(url);
  }
  static getRequests(data) {
    const url = "/api/warehouserequests/getrequests?pageno=" + data.pageNo;
    return http.get(url);

  }
}



